import React from 'react';

import List from './List';
import PaymentCard from './PaymentCard';
import { useTranslation } from '../providers/TranslationProvider';

function PaymentCardList(props) {
  const { cards, defaultCardId, onDelete, onEdit, onDefault } = props;

  const { t } = useTranslation();

  return (
    <List
      values={cards}
      renderItem={(card) => (
        <PaymentCard
          card={card}
          onDefault={onDefault}
          onDelete={onDelete}
          onEdit={onEdit}
          isDefault={card.id === defaultCardId}
        />
      )}
      noRecordLabel={t('No record found')}
    />
  );
}

PaymentCardList.defaultProps = {
  cards: [],
  onDelete: () => {},
  onEdit: () => {},
};

export default PaymentCardList;
