import React, { useEffect } from 'react';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';

import Paper from '../components/Paper';
import { useTranslation } from '../providers/TranslationProvider';

export const MESSAGE_TYPE = {
  SUCCESS: 'success',
  FAILURE: 'failure',
};

const useCheckoutMessageContainerStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(),
    minHeight: theme.spacing(30),
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'column',
  },
  button: {
    marginTop: theme.spacing(2),
  },
  success: {
    color: theme.palette.success.main,
  },
  failure: {
    color: theme.palette.error.main,
  },
}));

function CheckoutMessageContainer(props) {
  const { match, onSuccessRedirect, onFailureRedirect, onSuccessMount } = props;

  const { params } = match;
  const { messageType = MESSAGE_TYPE.SUCCESS } = params;

  const { t } = useTranslation();
  const classes = useCheckoutMessageContainerStyles();

  let {
    successMessage = t('Your payment has been successfully completed'),
    successButtonLabel = t('Continue shopping'),
    failureMessage = t('Your payment could not be completed'),
    failureButtonLabel = t('Continue checkout'),
  } = props;

  let content = null;

  switch (messageType) {
    case MESSAGE_TYPE.SUCCESS:
      content = (
        <>
          <Typography variant="h5" className={classes.success}>
            {successMessage}
          </Typography>
          <Button onClick={onSuccessRedirect} className={classes.button}>
            {successButtonLabel}
          </Button>
        </>
      );
      break;
    case MESSAGE_TYPE.FAILURE:
      content = (
        <>
          <Typography variant="h5" className={classes.failure}>
            {failureMessage}
          </Typography>
          <Button onClick={onFailureRedirect} className={classes.button}>
            {failureButtonLabel}
          </Button>
        </>
      );
      break;
    default:
      content = null;
  }

  useEffect(() => {
    if (messageType === MESSAGE_TYPE.SUCCESS && onSuccessMount) {
      onSuccessMount();
    }
  }, [messageType, onSuccessMount]);

  return <Paper className={classes.paper}>{content}</Paper>;
}

CheckoutMessageContainer.defaultProps = {
  onFailureRedirect: () => {},
  onSuccessRedirect: () => {},
};

export default CheckoutMessageContainer;
