import React, { useMemo } from 'react';
import { makeStyles } from '@material-ui/core/styles';

import Input from './Input';
import Select from './Select';

const useStyles = makeStyles(() => ({
  input: {
    '&[type=number]': {
      MozAppearance: 'textfield',
      '&::-webkit-outer-spin-button, &::-webkit-inner-spin-button': {
        WebkitAppearance: 'none',
        margin: 0,
      },
    },
  },
}));

const QuantityInput = ({
  maxItems,
  moreLabel,
  name,
  onChange,
  value,
  ...props
}) => {
  const [moreThanMax, setMoreThanMax] = React.useState(value > maxItems);
  const inputRef = React.useRef();
  const classes = useStyles();

  const isDecimal = useMemo(() => value % 1 !== 0, [value]);

  React.useEffect(() => {
    if (moreThanMax) {
      inputRef.current.focus();
    }
  }, [moreThanMax]);

  const handleMoreThanMax = React.useCallback(() => {
    setMoreThanMax(true);
  }, []);

  const handleChange = React.useCallback(
    (e) => {
      if (e.target.value === 'more') {
        handleMoreThanMax();
        onChange({ target: { value: maxItems, name } });
      } else {
        onChange(e);
        if (!moreThanMax) {
          // remove focus to delay selection lag
          document.activeElement &&
            document.activeElement.blur &&
            document.activeElement.blur();
        }
      }
    },
    [handleMoreThanMax, moreThanMax, onChange, maxItems, name]
  );

  if (moreThanMax || isDecimal) {
    return (
      <Input
        ref={inputRef}
        // InputProps={{
        inputProps={{
          className: classes.input,
        }}
        // }}
        onChange={handleChange}
        name={name}
        value={value}
        {...props}
        type="number"
        min={1}
      />
    );
  } else {
    return (
      <Select
        name={name}
        options={[
          ...[...Array(maxItems).keys()].map((i) => ({
            value: i + 1,
            label: i + 1,
          })),
          { value: 'more', label: moreLabel },
        ]}
        onChange={handleChange}
        value={value}
        {...props}
      />
    );
  }
};

QuantityInput.defaultProps = {
  maxItems: 10,
  moreLabel: 'More',
};

export default QuantityInput;
