import { useCallback, useMemo, useRef, useState } from 'react';

export default function useInfiniteLoading({
  limit = 10,
  fetchAPI,
  transform = (item) => item,
} = {}) {
  const [data, setData] = useState([]);
  const [total, setTotal] = useState(0);
  const [fetching, setFetching] = useState(false);

  const pageRef = useRef(1);

  const hasMore = useMemo(() => data.length < total, [data, total]);

  const fetchData = useCallback(
    (value, { append, reset } = {}) => {
      if (reset) {
        setData([]);
        setTotal(0);
        pageRef.current = 1;
      }
      setFetching(true);
      return fetchAPI(value, { page: pageRef.current, limit })
        .then(({ data = [], total }) => {
          setData((_data) =>
            append ? [..._data, ...transform(data)
            ] : transform(data)
          );
          setTotal(total);
        })
        .finally(() => {
          setFetching(false);
        });
    },
    [limit, fetchAPI, transform]
  );

  const loadMore = useCallback(
    (options) => {
      if (hasMore && !fetching) {
        pageRef.current += 1;
        return fetchData(options, { append: true });
      }
    },
    [hasMore, fetching, fetchData]
  );

  return useMemo(
    () => ({
      data,
      total,
      page: pageRef.current,
      fetching,
      fetchData,
      loadMore,
    }),
    [data, total, fetching, fetchData, loadMore]
  );
}
