import React from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';

import EshopViewContainer from '../containers/EshopView';

function Eshop(props) {
  const { match } = props;
  const { path } = match;

  return (
    <Switch>
      <Route path={path} component={EshopViewContainer} />
      <Redirect to={path} />
    </Switch>
  );
}

export default Eshop;
