import React from 'react';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import { makeStyles } from '@material-ui/core/styles';

import NextIcon from '@material-ui/icons/ChevronRight';
import PreviousIcon from '@material-ui/icons/ChevronLeft';

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(0.5),
    display: 'flex',
    width: 'fit-content',
    alignItems: 'center',
  },
}));

function Pagination(props) {
  const { onNext, onPrevious, label, disableNext, disablePrevious } = props;

  const classes = useStyles();

  return (
    <div className={classes.paper}>
      <Typography>{label}</Typography>
      &nbsp;&nbsp;
      <IconButton size="small" onClick={onPrevious} disabled={disablePrevious}>
        <PreviousIcon />
      </IconButton>
      &nbsp;&nbsp;
      <IconButton size="small" onClick={onNext} disabled={disableNext}>
        <NextIcon />
      </IconButton>
      &nbsp;&nbsp;
    </div>
  );
}

export default Pagination;
