import React from 'react';
import clsx from 'clsx';
import MuiChip from '@material-ui/core/Chip';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  chip: {
    color: '#616161',
  },
  icon: {
    backgroundColor: `${theme.palette.secondary.main}09`,
  },
  iconSelected: {
    color: theme.palette.secondary.main,
  },
  chipSelected: {
    color: theme.palette.secondary.main,
    backgroundColor: theme.palette.secondary.light,
  },
  chipClickable: {
    '&:focus, &:hover': {
      backgroundColor: theme.palette.secondary.light,
    },
  },
}));

function Chip(props) {
  const { icon: Icon, label, isSelected, onClick, className } = props;

  const classes = useStyles();

  return (
    <MuiChip
      label={label}
      icon={
        Icon && (
          <Icon
            fontSize="small"
            classes={{
              root: clsx(classes.icon, {
                [classes.iconSelected]: isSelected,
              }),
            }}
          />
        )
      }
      classes={{
        root: clsx(
          classes.chip,
          {
            [classes.chipSelected]: isSelected,
          },
          className
        ),
        clickable: classes.chipClickable,
      }}
      onClick={onClick}
    />
  );
}

Chip.defaultProps = {
  Icon: () => null,
  onClick: () => {},
};

export default Chip;
