import React, { useMemo } from 'react';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import { makeStyles } from '@material-ui/core/styles';

import Paper from './Paper';
import { useTranslation } from '../providers/TranslationProvider';

const IMAGE_WIDTH = 138;

const useProductTagStyles = makeStyles((theme) => ({
  tag: {
    position: 'absolute',
    padding: theme.spacing(0.5),
    backgroundColor: theme.palette.secondary.main,
    color: '#FFFFFF',
    right: 0,
    bottom: (props) => theme.spacing(2),
  },
}));

function ProductTag(props) {
  const { product } = props;

  const classes = useProductTagStyles();
  const { t } = useTranslation();

  if (product.unavailable) {
    return (
      <Box boxShadow={3} className={classes.tag}>
        {t('Unavailable')}
      </Box>
    );
  }

  if (product._displayDiscountPercentage) {
    return (
      <Box boxShadow={3} className={classes.tag}>
        {product._displayDiscountPercentage}
      </Box>
    );
  }

  return null;
}

const useProductInformation = makeStyles((theme) => ({
  container: {
    padding: theme.spacing(2),
    overflow: 'hidden',
    display: 'flex',
    flexDirection: 'column',
    flexGrow: 1,
    justifyContent: 'space-between',
    minHeight: theme.spacing(6),
  },
  productName: {
    fontWeight: 'bold',
  },
  productDescription: {
    display: '-webkit-box',
    '-webkit-box-orient': 'vertical',
    '-webkit-line-clamp': 2,
    overflow: 'hidden',
    '& > *:first-child': {
      margin: 0,
    },
  },
  productPrice: {
    color: theme.palette.secondary.main,
    marginTop: theme.spacing(),
  },
  productDiscountedDisplayPrice: {
    textDecoration: 'line-through',
    color: 'black',
  },
}));

function ProductInformation(props) {
  const { product = {} } = props;

  const hasImage = product && product.images && product.images[0];

  const classes = useProductInformation({ hasImage });

  const productPrice = product._displayDiscountPrice ? (
    <>
      <span className={classes.productDiscountedDisplayPrice}>
        {product._displayPrice}
      </span>
      &nbsp;&nbsp;
      {product._displayDiscountPrice}
    </>
  ) : (
    product._displayPrice
  );

  const description = useMemo(() => {
    var p = document.createElement('p');
    p.innerHTML = product.description;
    return p.innerText;
  }, [product.description]);

  return (
    <div className={classes.container}>
      <Box pb={1}>
        <Typography variant="h5" className={classes.productName} noWrap>
          {product.name}
        </Typography>
      </Box>
      <Box className={classes.productDescription}>
        <Typography>{description}</Typography>
      </Box>
      <Typography variant="body2" className={classes.productPrice}>
        {productPrice}
      </Typography>
    </div>
  );
}

const useProductImageStyles = makeStyles((theme) => ({
  productImage: ({ image }) => ({
    position: 'relative',
    display: 'flex',
    justifyContent: 'center',
    width: IMAGE_WIDTH,
    overflow: 'hidden',
    flexShrink: 0,
    backgroundPosition: 'center',
    backgroundImage: `url(${image})`,
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'contain',
  }),
}));

function ProductImage(props) {
  const { product } = props;
  const image = product && product.images && product.images[0];
  const classes = useProductImageStyles({ image });

  return image ? <div className={classes.productImage} /> : null;
}

const useStyles = makeStyles((theme) => ({
  paper: {
    display: 'flex',
    cursor: 'pointer',
    height: theme.spacing(18.25),
  },
}));

function ProductCard(props) {
  const { product = {}, onClick } = props;

  const classes = useStyles();

  const handleClick = React.useCallback(
    (e) => {
      onClick && onClick(e, product.id);
    },
    [onClick, product]
  );

  return (
    <Paper className={classes.paper} onClick={handleClick}>
      <ProductInformation product={product} />
      <ProductImage product={product} />
      <ProductTag product={product} />
    </Paper>
  );
}

export default ProductCard;
