import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';

import AddressListContainer from '../containers/AddressList';
import AddressFormContainer from '../containers/AddressForm';

function Address(props) {
  const { match, history } = props;
  const { path, url } = match;

  const newAddressPath = path + '/new';
  const editAddressPath = path + '/:addressId';
  const addressListPath = path;

  const handleRedirect = React.useCallback(
    (path, replace) => {
      const cb = replace ? history.replace : history.push;
      cb(path);
    },
    [history]
  );

  const redirectToNewAddress = React.useCallback(() => {
    handleRedirect(`${url}/new`);
  }, [url, handleRedirect]);

  const redirectToEditAddress = React.useCallback(
    (e, address) => {
      handleRedirect(`${url}/${address.id}`);
    },
    [url, handleRedirect]
  );

  const redirectToAddressList = React.useCallback(() => {
    handleRedirect(url, true);
  }, [url, handleRedirect]);

  const withRouterProps = React.useCallback(
    (Component, props = {}) => (routerProps) => (
      <Component {...routerProps} {...props} />
    ),
    []
  );

  return (
    <Switch>
      <Route
        path={newAddressPath}
        render={withRouterProps(AddressFormContainer, {
          redirectToAddressList,
        })}
      />
      <Route
        path={editAddressPath}
        render={withRouterProps(AddressFormContainer, {
          redirectToAddressList,
        })}
      />
      <Route
        path={addressListPath}
        render={withRouterProps(AddressListContainer, {
          onNew: redirectToNewAddress,
          onEdit: redirectToEditAddress,
        })}
      />

      <Redirect to={path} />
    </Switch>
  );
}

export default Address;
