import React from 'react';
import { CssBaseline } from '@material-ui/core';
import {
  createMuiTheme,
  ThemeProvider as MuiThemeProvider,
  makeStyles as muiMakeStyles,
} from '@material-ui/core/styles';

export const COLORS = {
  darkBlue: '#293847',
  grey: '#9E9E9E',
  green: '#3ECF8E',
  lightGreen: 'rgba(62, 207, 142, 0.1)',
};

const theme = createMuiTheme({
  palette: {
    primary: {
      main: COLORS.darkBlue,
    },
    secondary: {
      light: COLORS.lightGreen,
      main: COLORS.green,
      contrastText: '#FFFFFF',
    },
  },
  overrides: {
    MuiTypography: {
      colorTextPrimary: { color: COLORS.darkBlue },
    },
    MuiButton: {
      root: {
        borderRadius: 20,
      },
    },
  },
  props: {
    MuiInput: {
      disableUnderline: true,
    },
    MuiTextField: {
      variant: 'outlined',
      margin: 'dense',
      fullWidth: true,
    },
    MuiButton: {
      variant: 'contained',
      color: 'secondary',
    },
  },
  custom: {
    COLORS,
  },
});

function ThemeProvider({ children }) {
  return (
    <MuiThemeProvider theme={theme}>
      <CssBaseline />
      {children}
    </MuiThemeProvider>
  );
}

export const makeStyles = muiMakeStyles;

export default ThemeProvider;
