import React from 'react';
import { Route, Switch } from 'react-router-dom';

import PaymentCardFormContainer from '../containers/PaymentCardForm';
import PaymentCardListContainer from '../containers/PaymentCardList';

function PaymentCard(props) {
  const { match, history } = props;
  const { path, url } = match;

  const newPaymentCardPath = path + '/new';
  const editPaymentCardPath = path + '/:cardId';
  const paymentCardListPath = path;

  const handleRedirect = React.useCallback(
    (path, replace) => {
      const cb = replace ? history.replace : history.push;
      cb(path);
    },
    [history]
  );

  const redirectToNewCard = React.useCallback(() => {
    handleRedirect(`${url}/new`);
  }, [url, handleRedirect]);

  const redirectToEditCard = React.useCallback(
    (e, card) => {
      handleRedirect(`${url}/${card.id}`);
    },
    [url, handleRedirect]
  );

  const redirectToCardList = React.useCallback(() => {
    handleRedirect(url, true);
  }, [url, handleRedirect]);

  const withRouterProps = React.useCallback(
    (Component, props = {}) => (routerProps) => (
      <Component {...routerProps} {...props} />
    ),
    []
  );

  return (
    <Switch>
      <Route
        exact
        path={newPaymentCardPath}
        render={withRouterProps(PaymentCardFormContainer, {
          redirectToCardList,
        })}
      />
      <Route
        exact
        path={editPaymentCardPath}
        render={withRouterProps(PaymentCardFormContainer, {
          redirectToCardList,
        })}
      />
      <Route
        exact
        path={paymentCardListPath}
        render={withRouterProps(PaymentCardListContainer, {
          onNew: redirectToNewCard,
          onEdit: redirectToEditCard,
        })}
      />
    </Switch>
  );
}

export default PaymentCard;
