import qs from 'qs';

import RestService from '../services/RestService';

export const PRODUCT_TYPESELECT_SERVICE = 'service';

function getMetafileURL(metafileId) {
  return `./${RestService.instance.defaults.baseURL}ws/portal/metafile/${metafileId}`; //`/ws/portal/metafile/${metafileId}` for portal
}

function getQueryString(query) {
  return qs.stringify(query);
}

function handleResponse(res) {
  if (res && res.status === 0) {
    return res;
  } else {
    throw new Error(res && res.message);
  }
}

function handleImageURL(product) {
  const hasPictures = product._pictures && product._pictures.length;
  product.images = hasPictures ? product._pictures.map(getMetafileURL) : [];
  return product;
}

function handleDisplayPrice(product) {
  const symbol = product && product._currency && product._currency.symbol;
  if (symbol) {
    if (product._previousPrice) {
      product._displayPrice = symbol + product._previousPrice;
      product._displayDiscountPrice = symbol + product._price;
    } else {
      product._displayPrice = symbol + product._price;
    }
  }
  return product;
}

function handleDiscount(product) {
  if (product._discount && product._discount.includes('%')) {
    product._displayDiscountPercentage = product._discount;
  }
  return product;
}

function handleUnavailablity(product) {
  if (product.productTypeSelect === PRODUCT_TYPESELECT_SERVICE) {
    return product;
  }

  if (!Number(product._realQty)) {
    product.unavailable = true;
  }
  return product;
}

function handleComplementaryProducts(product) {
  product.complementaryProducts = (product.complementaryProductList || [])
    .map(({ product = {}, qty, optional }) => ({
      id: product?.id,
      qty: Number(qty),
      name: product.name,
      optional,
    }))
    .filter((p) => p.id)
    .filter((p) => !isNaN(parseFloat(p.qty)));

  product.complementaryProductList = null;
  return product;
}

function handleProductInformation(product) {
  handleImageURL(product);
  handleDisplayPrice(product);
  handleDiscount(product);
  handleUnavailablity(product);
  handleComplementaryProducts(product);
  return product;
}

function handlePaginationTypeDataResponse() {
  return {
    data: [],
    total: 0,
  };
}

function handleEmptyObjectDataReponse() {
  return {};
}

function handleEmptyArrayDataResponse() {
  return [];
}

function handleUndefinedDataResponse(res, defaultValue = []) {
  res.data = res.data || defaultValue;
  return res;
}

function getDisplayAddress(addressEntity) {
  const {
    address,
    addressComplement,
    zipCode,
    countryName,
    cityName,
  } = addressEntity;

  return `${address || ''} ${addressComplement || ''} ${cityName || ''} ${
    zipCode || ''
  } ${countryName || ''}`;
}

function mapCheckout(checkout) {
  const currencySymbol = getPurchaseCurrencySymbol(checkout);

  const invoicingAddress = getInverseMappedAddress(
    checkout.mainInvoicingAddress
  );
  const deliveryAddress = getInverseMappedAddress(checkout.deliveryAddress);

  invoicingAddress.displayAddress = getDisplayAddress(invoicingAddress);
  deliveryAddress.displayAddress = getDisplayAddress(deliveryAddress);

  return {
    id: checkout.id,
    date: checkout.checkoutDate,
    total: checkout.inTaxTotal || '0.00',
    totalTax: checkout.taxTotal || '0.00',
    totalWithoutTax: checkout.exTaxTotal || '0.00',
    totalDiscount: checkout._discountTotal || '0.00',
    currency: checkout.currency,
    invoicingAddress,
    deliveryAddress,
    lines: getLines(checkout.saleOrderLineList),
    itemsChanged: checkout.itemsChanged,
    original: { total: checkout.inTaxTotal },
    paymentModes: checkout.paymentModes || [],
  };

  function getLines(lines = []) {
    return lines.map((line) => {
      return {
        id: line.id,
        name: line.productName,
        qty: toFixed(line.qty),
        unitPrice: currencySymbol
          ? addSuffix(toFixed(line.price), currencySymbol)
          : toFixed(line.price),
        vat:
          line.taxLine && line.taxLine.value
            ? addSuffix(toFixed(line.taxLine.value * 100), '%')
            : '',
        discount: currencySymbol
          ? addSuffix(toFixed(line._discountTotal || '0.00'), currencySymbol)
          : '0.00',

        total: currencySymbol
          ? addSuffix(toFixed(line.inTaxTotal), currencySymbol)
          : toFixed(line.inTaxTotal),
        productId: line.product && line.product.id,
        productTypeSelect: line.product && line.product.productTypeSelect,
      };
    });
  }
}

function toFixed(value, point = 2) {
  return Number.parseFloat(value).toFixed(point);
}

function addSuffix(str, suffix, withSpace = true) {
  return withSpace ? str + ' ' + suffix : str + suffix;
}

function getPurchaseCurrencySymbol(entity) {
  return entity && entity.currency && entity.currency.symbol;
}

function mapCurrencySymbol(entity) {
  const currencySymbol = getPurchaseCurrencySymbol(entity);

  if (!currencySymbol) {
    return entity;
  }

  const addCurrencySymbol = (str) => addSuffix(str, currencySymbol);

  return Object.assign(entity, {
    total: addCurrencySymbol(entity.total),
    totalTax: addCurrencySymbol(entity.totalTax),
    totalWithoutTax: addCurrencySymbol(entity.totalWithoutTax),
    totalDiscount: addCurrencySymbol(entity.totalDiscount),
  });
}

function handleCheckoutInformation(checkout) {
  return mapCurrencySymbol(mapCheckout(checkout));
}

function handleCheckoutItemChange(checkout) {
  const { itemsChanged } = checkout;

  if (itemsChanged) {
    checkout.lines = checkout.lines.filter(
      (line) =>
        line.productTypeSelect !== PRODUCT_TYPESELECT_SERVICE &&
        line.qty !== '0.00'
    );
  }

  return checkout;
}

function handleReturnDataFromResponse(res) {
  return res && res.data;
}

function handleTransformDataResponse(res, transform) {
  if (res.data) {
    res.data = transform(res.data);
  }
  return res;
}

function handleAddDisplayAddress(address) {
  return Object.assign({}, address, {
    displayAddress: getDisplayAddress(address),
  });
}

function handleTransformProductsInformation(res) {
  const transform = (products) => products.map(handleProductInformation);
  return handleTransformDataResponse(res, transform);
}

function handleTransformCardsInformation(res) {
  const transform = (cards) => cards.map(handleCardInformation);
  return handleTransformDataResponse(res, transform);
}

function handleTransformInverseMapAddress(res) {
  const transform = (addresses) => addresses.map(getInverseMappedAddress);
  return handleTransformDataResponse(res, transform);
}

function handleTransformDisplayAddress(res) {
  const transform = (addresses) => addresses.map(handleAddDisplayAddress);
  return handleTransformDataResponse(res, transform);
}

function manageQueryString(url, query) {
  if (query) {
    return url + '?' + getQueryString(query);
  } else {
    return url;
  }
}

function fetchProducts(query) {
  const URL = manageQueryString(`/ws/portal/products`, query);
  return RestService.get(URL)
    .then(handleResponse)
    .then(handleUndefinedDataResponse)
    .then(handleTransformProductsInformation)
    .catch(handlePaginationTypeDataResponse);
}

function fetchProduct(productId) {
  const URL = `/ws/portal/products/${productId}`;
  return RestService.get(URL)
    .then(handleResponse)
    .then((result) => handleUndefinedDataResponse(result, {}))
    .then(handleReturnDataFromResponse)
    .then(handleProductInformation)
    .catch(handleEmptyObjectDataReponse);
}

function fetchCategories() {
  const URL = `/ws/portal/categories`;
  return RestService.get(URL)
    .then(handleResponse)
    .then(handleUndefinedDataResponse)
    .then(handleReturnDataFromResponse)
    .catch(handleEmptyArrayDataResponse);
}

function fetchCategoryProducts(categoryId, query) {
  const URL = manageQueryString(
    `/ws/portal/categories/${categoryId}/products`,
    query
  );
  return RestService.get(URL)
    .then(handleResponse)
    .then(handleUndefinedDataResponse)
    .then(handleTransformProductsInformation)
    .catch(handlePaginationTypeDataResponse);
}

function fetchAddresses() {
  const URL = `/ws/portal/addresses`;
  return RestService.get(URL)
    .then(handleResponse)
    .then(handleUndefinedDataResponse)
    .then(handleTransformInverseMapAddress)
    .then(handleTransformDisplayAddress)
    .catch(handlePaginationTypeDataResponse);
}

function fetchAddress(addressId) {
  const URL = `/ws/portal/addresses/${addressId}`;
  return RestService.get(URL)
    .then(handleResponse)
    .then(handleReturnDataFromResponse)
    .then(getInverseMappedAddress);
}

function getMappedAddress(address) {
  return {
    addressL4: address.address,
    addressL2: address.addressComplement,
    zip: address.zipCode,
    cityId: address.city,
    id: address.id,
    addressL7CountryId: address.country,
  };
}

function getInverseMappedAddress(address) {
  const { city, addressL7Country } = address;
  return {
    address: address.addressL4,
    addressComplement: address.addressL2,
    zipCode: address.zip,
    city: address.cityId || (city && city.id),
    id: address.id,
    country:
      address.addressL7CountryId || (addressL7Country && addressL7Country.id),
    cityName: (city && city.name) || '',
    countryName: (addressL7Country && addressL7Country.name) || '',
  };
}

function addAddress(address) {
  const URL = '/ws/portal/addresses';
  return RestService.post(URL, getMappedAddress(address))
    .then(handleResponse)
    .then(handleReturnDataFromResponse);
}

function updateAddress(addressId, address) {
  const URL = `/ws/portal/addresses/${addressId}`;
  return RestService.post(URL, getMappedAddress(address))
    .then(handleResponse)
    .then(handleReturnDataFromResponse);
}

function deleteAddress(addressId) {
  const URL = `/ws/portal/addresses/${addressId}`;
  return RestService.delete(URL);
}

function fetchCities(countryId, query) {
  const URL = manageQueryString(
    countryId
      ? `/ws/portal/countries/${countryId}/cities`
      : '/ws/portal/cities',
    query
  );
  return RestService.get(URL).then(handleResponse);
}

function fetchCountries() {
  const URL = '/ws/portal/countries';
  return RestService.get(URL)
    .then(handleResponse)
    .then(handleUndefinedDataResponse)
    .then(handleReturnDataFromResponse)
    .catch(handleEmptyArrayDataResponse);
}

function checkCart(cart) {
  const payload = {
    cart: Object.keys(cart).map((productId) => ({
      productId: Number(productId),
      quantity: cart[productId],
    })),
  };

  const URL = '/ws/portal/cart/check-cart';

  return RestService.post(URL, payload)
    .then(handleResponse)
    .then(handleReturnDataFromResponse)
    .then(handleCheckoutInformation)
    .then(handleCheckoutItemChange)
    .catch(handleEmptyObjectDataReponse);
}

function createCheckoutOrderData(data) {
  const { deliveryAddress, invoicingAddress, cart } = data;

  const orderData = {
    deliveryAddressId: deliveryAddress.id,
    mainInvoicingAddressId: invoicingAddress.id,
    cart: Object.keys(cart).reduce((_cart, productId) => {
      _cart.push({ productId: Number(productId), quantity: cart[productId] });
      return _cart;
    }, []),
  };

  return orderData;
}

function checkoutPaypal(data) {
  const { paypalPaymentData, orderData } = data;

  const paypalOrderId = paypalPaymentData.id;

  const URL = '/ws/portal/cart/checkout/paypal';

  const _orderData = createCheckoutOrderData(orderData);

  return RestService.post(URL, {
    paypalOrderId,
    orderData: _orderData,
  })
    .then(handleResponse)
    .then(handleReturnDataFromResponse);
}

function checkoutPaymentCard(data) {
  const { cardId, orderData } = data;

  const URL = '/ws/portal/cart/checkout/stripe';

  const _orderData = createCheckoutOrderData(orderData);

  const payload = {
    orderData: _orderData,
  };

  if (cardId) {
    payload.cardId = cardId;
  }

  return RestService.post(URL, payload)
    .then(handleResponse)
    .then(handleReturnDataFromResponse);
}

function handleCardInformation(card) {
  const { expiry = '', brand } = card;
  const expiryMonth = expiry.slice(0, expiry.length - 2);
  const expiryYear = expiry.slice(expiry.length - 2);
  const expiryDisplay = `${expiryMonth}/${expiryYear}`;

  let { cardNumber = '' } = card;
  cardNumber = String(cardNumber);
  const last4 = cardNumber && cardNumber.slice(cardNumber.length - 4);

  return {
    ...card,
    expiryDisplay,
    last4,
    brand: brand || '',
  };
}

function fetchPaymentCards() {
  const URL = '/ws/portal/payment-cards';
  return RestService.get(URL)
    .then(handleResponse)
    .then(handleUndefinedDataResponse)
    .then(handleTransformCardsInformation)
    .catch(handlePaginationTypeDataResponse);
}

function fetchPaymentCard(cardId) {
  const URL = `/ws/portal/payment-cards/${cardId}`;
  return RestService.get(URL)
    .then(handleResponse)
    .then(handleReturnDataFromResponse)
    .then(handleCardInformation);
}

function addPaymentCard(card) {
  const URL = '/ws/portal/payment-cards';
  return RestService.post(URL, card).then(handleResponse);
}

function updatePaymentCard(cardId, card) {
  const URL = `/ws/portal/payment-cards/${cardId}`;
  return RestService.post(URL, card).then(handleResponse);
}

function deletePaymentCard(cardId) {
  const URL = `/ws/portal/payment-cards/${cardId}`;
  return RestService.delete(URL).then((res) => {
    if (res.status === -1) {
      throw new Error(res && res.message);
    }
  });
}

function fetchDefaultPaymentCard() {
  const URL = '/ws/portal/payment-cards/default';
  return RestService.get(URL)
    .then(handleResponse)
    .then(handleReturnDataFromResponse);
}

function updateDefaultPaymentCard(cardId) {
  const URL = `/ws/portal/payment-cards/default/${cardId}`;
  return RestService.post(URL).then(handleResponse);
}

function fetchPaypalMerchantId() {
  const URL = '/ws/portal/config/merchant-id';
  return RestService.get(URL)
    .then(handleResponse)
    .then(handleReturnDataFromResponse)
    .catch(handleEmptyObjectDataReponse);
}

function fetchTranslations(data) {
  const URL = '/ws/portal/translate';
  return RestService.post(URL, data)
    .then((result) => {
      if (!(result && result.status === 0)) {
        result.data = [];
      }

      const transform = (arr) =>
        arr.reduce((acc, value) => {
          acc[value.key] = value.message;
          return acc;
        }, {});

      const { data } = result;
      return Array.isArray(data) ? transform(data) : {};
    })
    .catch(() => {
      return {};
    });
}

function checkoutPaybox(data, redirectURL = window.location.href) {
  const { orderData } = data;
  const URL = '/ws/portal/cart/checkout/paybox';
  const _orderData = createCheckoutOrderData(orderData);

  const payload = {
    orderData: _orderData,
    successURL: redirectURL + '/success',
    failureURL: redirectURL + '/failure',
  };

  return RestService.post(URL, payload)
    .then(handleResponse)
    .then(handleReturnDataFromResponse);
}

function fetchConfiguration() {
  return RestService.get(`/ws/portal/config`)
    .then(handleResponse)
    .then(handleReturnDataFromResponse)
    .then(
      ({
        isQuotationRequestActive,
        isDataPolicyRequired,
        dataPolicyMetaFile,
        isReturnPolicyRequired,
        returnPolicyMetaFile,
        isTermRequired,
        termMetaFile,
      }) => {
        return {
          isQuotationRequestActive,
          isDataPolicyRequired,
          dataPolicyMetaFile,
          isReturnPolicyRequired,
          returnPolicyMetaFile,
          isTermRequired,
          termMetaFile,
        };
      }
    )
    .catch(() => ({ isQuotationRequestActive: false }));
}

function checkoutQuotation(data) {
  const { orderData } = data;

  const URL = '/ws/portal/cart/checkout/quotation';

  const _orderData = createCheckoutOrderData(orderData);

  const payload = {
    orderData: _orderData,
  };

  return RestService.post(URL, payload)
    .then(handleResponse)
    .then(handleReturnDataFromResponse);
}

function fetchPaymentCardConfiguration() {
  const URL = '/ws/portal/config/is-card-active';

  return RestService.get(URL)
    .then(handleResponse)
    .then(handleReturnDataFromResponse)
    .then(({ isStripeActivated: isPaymentCardActivated }) => ({
      isPaymentCardActivated,
    }));
}

function fetchInfo() {
  return RestService.checkInfo();
}

export default {
  fetchProducts,
  fetchProduct,
  fetchCategories,
  fetchCategoryProducts,
  fetchAddresses,
  fetchAddress,
  addAddress,
  deleteAddress,
  updateAddress,
  fetchCities,
  fetchCountries,
  checkCart,
  checkoutPaypal,
  checkoutPaybox,
  checkoutPaymentCard,
  checkoutQuotation,
  fetchPaymentCards,
  fetchPaymentCard,
  addPaymentCard,
  updatePaymentCard,
  deletePaymentCard,
  fetchPaymentCardConfiguration,
  fetchDefaultPaymentCard,
  updateDefaultPaymentCard,
  fetchPaypalMerchantId,
  fetchTranslations,
  fetchConfiguration,
  fetchInfo,
  getMetafileURL,
};
