import React, { useEffect, useCallback, useState } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';

import CheckoutContainer from './Checkout';
import CheckoutMessageContainer from './CheckoutMessage';
import ProductViewContainer from './ProductView';
import ProductListContainer from './ProductList';
import { useEshop } from '../providers/EshopProvider';
import EshopAPI from '../EshopAPI';
import Loader from '../components/Loader';

function EshopViewContainer(props) {
  const { match, history } = props;
  const { path, url } = match;

  const [loading, setLoading] = useState(true);
  const { clearCart, setConfig } = useEshop();

  const handleRedirectToCheckout = useCallback(() => {
    history.replace(`${url}/checkout`);
  }, [history, url]);

  const handleRedirectToEshop = useCallback(() => {
    history.replace(url);
  }, [history, url]);

  const handleRedirectToCheckoutMessage = useCallback(
    (messageType) => {
      history.replace(`${url}/checkout/${messageType}`);
    },
    [history, url]
  );

  const fetchConfiguration = useCallback(() => {
    EshopAPI.fetchConfiguration()
      .then(setConfig)
      .finally(() => setLoading(false));
  }, [setConfig]);

  useEffect(() => {
    fetchConfiguration();
  }, [fetchConfiguration]);

  if (loading) {
    return <Loader />;
  }

  return (
    <Switch>
      <Route
        path={`${path}/checkout/:messageType`}
        render={(props) => (
          <CheckoutMessageContainer
            {...props}
            onSuccessMount={clearCart}
            onSuccessRedirect={handleRedirectToEshop}
            onFailureRedirect={handleRedirectToCheckout}
          />
        )}
      />
      <Route
        path={`${path}/checkout`}
        render={(props) => (
          <CheckoutContainer
            {...props}
            onRedirectToCheckoutMessage={handleRedirectToCheckoutMessage}
          />
        )}
      />
      <Route
        path={`${path}/product/:productId`}
        component={ProductViewContainer}
      />
      <Route path={path} component={ProductListContainer} />
      <Redirect to={path} />
    </Switch>
  );
}

export default EshopViewContainer;
