import React, { useState, useEffect } from 'react';
import { useSnackbar } from 'notistack';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';

import ConfirmationDialog from '../components/ConfirmationDialog';
import AddressList from '../components/AddressList';
import Loader from '../components/Loader';
import EshopAPI from '../EshopAPI';
import { useTranslation } from '../providers/TranslationProvider';

const useStyles = makeStyles((theme) => ({
  header: {
    display: 'flex',
    justifyContent: 'space-between',
    marginBottom: theme.spacing(2),
  },
  headerAction: {
    fontSize: '0.7rem',
  },
}));

function AddressListContainer(props) {
  const { onNew, onEdit } = props;

  const classes = useStyles();
  const { t } = useTranslation();
  const { enqueueSnackbar } = useSnackbar();

  const [{ loading, addresses, address, dialogOpen }, setStore] = useState({
    addresses: [],
    address: null,
    loading: true,
    dialogOpen: false,
  });

  function fetchAddresses() {
    EshopAPI.fetchAddresses()
      .then(({ data }) => {
        setStore((store) => ({
          ...store,
          addresses: data,
          loading: false,
        }));
      })
      .catch(() => {
        setStore((store) => ({ ...store, loading: false }));
      });
  }

  function handleOpenDeleteDialog(e, address) {
    setStore((store) => ({
      ...store,
      address,
      dialogOpen: true,
    }));
  }

  function handleCloseDeleteDialog() {
    setStore((store) => ({ ...store, address: null, dialogOpen: false }));
  }

  function handleRemoveAddressFromList(addressId) {
    setStore((store) => ({
      ...store,
      addresses: addresses.filter((address) => address.id !== addressId),
    }));
  }

  function handleDeleteAddress() {
    const addressId = address.id;
    EshopAPI.deleteAddress(addressId)
      .then(() => handleRemoveAddressFromList(addressId))
      .catch(() => {
        enqueueSnackbar(t('Error'), { variant: 'error' });
      })
      .finally(handleCloseDeleteDialog);
  }

  useEffect(() => {
    fetchAddresses();
  }, []);

  if (loading) {
    return <Loader />;
  }

  return (
    <>
      <div className={classes.header}>
        <Typography variant="h5">{t('Addresses')}</Typography>
        <Button className={classes.headerAction} onClick={onNew}>
          {t('New address')}
        </Button>
      </div>
      <AddressList
        addresses={addresses}
        onDelete={handleOpenDeleteDialog}
        onEdit={onEdit}
      />
      <ConfirmationDialog
        onConfirm={handleDeleteAddress}
        onClose={handleCloseDeleteDialog}
        title={t('Confirm deletion')}
        open={dialogOpen}
      >
        <p>{address && address.address}</p>
      </ConfirmationDialog>
    </>
  );
}

AddressListContainer.defaultProps = {
  onNew: () => {},
  onEdit: () => {},
};

export default AddressListContainer;
