import React from 'react';
import i18next from 'i18next';
import {
  initReactI18next,
  I18nextProvider,
  useTranslation as i18nextUseTranslation,
} from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';
import Backend from 'i18next-http-backend';

import en from '../assets/locales/en/translations.json';
import fr from '../assets/locales/fr/translations.json';
import EshopAPI from '../EshopAPI';

const translations = {
  en,
  fr,
};

const i18n = i18next.createInstance();
const lng = navigator.language.includes('fr') ? 'fr' : 'en';

i18n
  .use(initReactI18next)
  .use(Backend)
  .use(LanguageDetector)
  .init({
    lng,
    fallbackLng: 'en',
    interpolation: {
      escapeValue: false,
    },
    backend: {
      request: (options, url, payload, callback) => {
        const getLanguageFromURL = (url) => {
          const regexURL = /\/locales\/(?<lang>\w+)\/translation.json/gm;
          return regexURL.exec(url).groups.lang || 'en';
        };

        const lang = getLanguageFromURL(url);
        const defaultTranslations = translations[lang] || translations.en;

        EshopAPI.fetchTranslations({
          keys: Object.keys(defaultTranslations),
          lang,
        }).then((result) => {

          const removeEmptyTranslation = (obj = {}) =>
            Object.keys(obj).reduce((acc, key) => {
              const value = obj[key];
              if (value) {
                acc[key] = value;
              }
              return acc;
            }, {});

          const data = JSON.stringify(
            Object.assign(
              {},
              defaultTranslations,
              removeEmptyTranslation(result)
            )
          );

          callback(null, {
            status: true,
            data,
          });
        });
      },
    },
    keySeparator: false,
  });

function TranslationProvider({ children }) {
  return <I18nextProvider i18n={i18n}>{children}</I18nextProvider>;
}

export default TranslationProvider;

export const useTranslation = i18nextUseTranslation;
