import React from 'react';

import SearchInput from './SearchInput';
import { useTranslation } from '../providers/TranslationProvider';

function useDebounce(cb, duration) {
  const timer = React.useRef(null);

  const clearTimer = () => timer.current && clearTimeout(timer.current);
  const setTimer = (cb) => (timer.current = setTimeout(cb, duration));

  React.useEffect(() => {
    return () => clearTimer();
  }, []);

  return (...args) => {
    clearTimer();
    setTimer(() => cb(...args));
  };
}

export default ({ value: valueProp, onChange }) => {
  const { t } = useTranslation();
  const [value, setValue] = React.useState(valueProp);

  const debounceChange = useDebounce(onChange, 300);

  const handleChange = (e) => {
    const { value } = e.target;
    setValue(value);
    debounceChange(value);
  };

  const handleSubmit = (e) => e.preventDefault();

  return (
    <form onSubmit={handleSubmit}>
      <SearchInput
        name="search"
        placeholder={t('Search')}
        onChange={handleChange}
        value={value}
      />
    </form>
  );
};
