import React, { useCallback, useRef, useState } from 'react';
import Box from '@material-ui/core/Box';
import MenuItem from '@material-ui/core/MenuItem';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { makeStyles, useTheme } from '@material-ui/core/styles';

import Input from './Input';
import Loader from './Loader';

const useSelectStyles = makeStyles(() => ({
  root: {
    '&:focus': {
      backgroundColor: 'white',
    },
  },
}));

const useMenuStyles = makeStyles(() => ({
  paper: {
    maxHeight: 300,
  },
}));

const useMenuItemStyles = makeStyles((theme) => ({
  root: {
    '&$selected': {
      color: theme.palette.secondary.main,
      backgroundColor: theme.palette.secondary.light,
    },
    '&:hover': {
      color: theme.palette.secondary.main,
      backgroundColor: theme.palette.secondary.light,
    },
  },
  selected: {},
}));

const usePopoverClasses = makeStyles((theme) => ({
  root: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  paper: {
    width: '90%',
    maxWidth: 400,
  },
}));

function Select(props) {
  const {
    options,
    onLoadMore,
    showLoadingOption,
    onOpen,
    onClose,
    ...rest
  } = props;

  const selectRef = useRef();
  const [anchorPosition, setAnchorPosition] = useState(null);

  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));

  const selectClasses = useSelectStyles();
  const menuClasses = useMenuStyles();
  const menuItemClasses = useMenuItemStyles();
  const popoverClasses = usePopoverClasses();

  const handleOpen = useCallback(
    (event) => {
      if (onLoadMore) {
        const { top, left } = selectRef?.current?.getBoundingClientRect();
        setAnchorPosition({ top, left });
      }
      if (onOpen) {
        onOpen(event);
      }
    },
    [onLoadMore, onOpen]
  );

  const handleClose = useCallback(
    (event) => {
      if (onLoadMore) {
        setAnchorPosition(null);
      }
      if (onClose) {
        onClose(event);
      }
    },
    [onLoadMore, onClose]
  );

  return (
    <Input
      select
      ref={selectRef}
      SelectProps={{
        classes: selectClasses,
        MenuProps: {
          classes: menuClasses,
          ...(fullScreen
            ? {
                anchorReference: 'none',
                PopoverClasses: popoverClasses,
                BackdropProps: { invisible: false },
              }
            : {
                ...(anchorPosition
                  ? { anchorReference: 'anchorPosition', anchorPosition }
                  : {}),
              }),

          onScroll: (event) => {
            const { scrollTop, scrollHeight, clientHeight } = event.target;
            const atScrollEnd = scrollTop === scrollHeight - clientHeight;
            if (atScrollEnd && onLoadMore) {
              onLoadMore();
            }
          },
        },
        onOpen: handleOpen,
        onClose: handleClose,
      }}
      {...rest}
    >
      {options.map((option) => (
        <MenuItem
          key={option.value}
          value={option.value}
          classes={menuItemClasses}
        >
          {option.label}
        </MenuItem>
      ))}
      {showLoadingOption && (
        <MenuItem disabled>
          <Box width="100%">
            <Loader />
          </Box>
        </MenuItem>
      )}
    </Input>
  );
}

Select.defaultProps = {
  options: [],
};

export default Select;
