import React, { useState, useEffect, useCallback } from 'react';
import { useSnackbar } from 'notistack';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';

import Loader from '../components/Loader';
import PaymentCardList from '../components/PaymentCardList';
import ConfirmationDialog from '../components/ConfirmationDialog';
import EshopAPI from '../EshopAPI';
import { useTranslation } from '../providers/TranslationProvider';

const usePaymentCardListStyles = makeStyles((theme) => ({
  header: {
    display: 'flex',
    justifyContent: 'space-between',
    marginBottom: theme.spacing(2),
  },
  headerAction: {
    fontSize: '0.7rem',
  },
  loader: {
    position: 'absolute',
    inset: 0,
    justifyContent: 'center',
    display: 'flex',
    alignItems: 'center',
    backgroundColor: 'rgba(0,0,0,0.4)',
    zIndex: 2,
  },
}));

function PaymentCardListContainer(props) {
  const { onNew, onEdit } = props;

  const { t } = useTranslation();
  const classes = usePaymentCardListStyles();
  const { enqueueSnackbar } = useSnackbar();

  const [cards, setCards] = useState([]);
  const [defaultPaymentCardId, setDefaultPaymentCardId] = useState();
  const [card, setCard] = useState();
  const [loading, setLoading] = useState(true);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [savingDefault, setSavingDefault] = useState(false);

  function fetchPaymentCards() {
    EshopAPI.fetchPaymentCards()
      .then((res) => {
        const cards = res.data;
        EshopAPI.fetchDefaultPaymentCard()
          .then((res) => {
            if (res.id) {
              setDefaultPaymentCardId(res.id);
            }
            setCards(cards);
            setLoading(false);
          })
          .catch(() => {
            setCards(cards);
            setLoading(false);
          });
      })
      .catch(() => {
        setLoading(false);
      });
  }

  useEffect(() => {
    fetchPaymentCards();
  }, []);

  function handleOpenDeleteDialog(e, card) {
    setCard(card);
    setDeleteDialogOpen(true);
  }

  function handleCloseDeleteDialog() {
    setCard(null);
    setDeleteDialogOpen(false);
  }

  function handleRemoveCardFromList(cardId) {
    setCards((cards) => cards.filter((card) => card.id !== cardId));
  }

  function handleDeleteCard() {
    const cardId = card.id;
    EshopAPI.deletePaymentCard(cardId)
      .then(() => handleRemoveCardFromList(cardId))
      .catch(() => {
        enqueueSnackbar(t('Error'), { variant: 'error' });
      })
      .finally(handleCloseDeleteDialog);
  }

  const handleUpdateDefaultCard = useCallback(
    (e, card) => {
      const cardId = card.id;
      setSavingDefault(true);
      return EshopAPI.updateDefaultPaymentCard(cardId)
        .then(() => {
          setDefaultPaymentCardId(cardId);
        })
        .catch(() => {
          enqueueSnackbar(t('Error'), { variant: 'error' });
        })
        .finally(() => {
          setSavingDefault(false);
        });
    },
    [enqueueSnackbar, t]
  );

  if (loading) {
    return <Loader />;
  }

  return (
    <>
      {savingDefault && (
        <div className={classes.loader}>
          <Loader />
        </div>
      )}
      <div className={classes.header}>
        <Typography variant="h5">{t('Credit card')}</Typography>
        <Button className={classes.headerAction} onClick={onNew}>
          {t('Add a credit card')}
        </Button>
      </div>
      <PaymentCardList
        cards={cards}
        defaultCardId={defaultPaymentCardId}
        onDelete={handleOpenDeleteDialog}
        onDefault={handleUpdateDefaultCard}
        onEdit={onEdit}
      />
      <ConfirmationDialog
        onConfirm={handleDeleteCard}
        onClose={handleCloseDeleteDialog}
        title={t('Confirm deletion')}
        open={deleteDialogOpen}
      >
        <p>{card && `      ${card.last4}`}</p>
      </ConfirmationDialog>
    </>
  );
}

export default PaymentCardListContainer;
