import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';

function List(props) {
  const {
    values,
    ValueUniqueKey = 'id',
    ItemContainerProps = {},
    noRecordLabel = 'No record found',
    renderItem = () => null,
  } = props;

  const getItemContainerProps = (value, index) => ({
    item: true,
    xs: 12,
    sm: 4,
    md: 3,
    key: value[ValueUniqueKey] || index,
    ...ItemContainerProps,
  });

  return (
    <Grid container spacing={3}>
      {values && values.length ? (
        values.map((value, index) => (
          <Grid {...getItemContainerProps(value, index)}>
            {renderItem(value)}
          </Grid>
        ))
      ) : (
        <Grid item xs={12}>
          <Typography>{noRecordLabel}</Typography>
        </Grid>
      )}
    </Grid>
  );
}

export default List;
