import React from 'react';
import { useTheme } from 'goovee-lib';
import { Switch, Route, Redirect } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';

import Eshop from './pages/Eshop';
import Address from './pages/Address';
import PaymentCard from './pages/PaymentCard';
import TopBar from './containers/Topbar';
import Loader from './components/Loader';
import EshopAPI from './EshopAPI';
import { useTranslation } from './providers/TranslationProvider';
import { useSession } from './providers/SessionProvider';

const useStyles = makeStyles((theme) => ({
  container: {
    position: 'relative',
    overflow: 'auto',
    height: (props) => `calc(100% - ${props.theme.spacingUnit * 6}px)`, //Height of topbar
    padding: (props) => props.theme.spacingUnit * 3,
    maxWidth: 1400,
    marginRight: 'auto',
    marginLeft: 'auto',
    [theme.breakpoints.down('sm')]: {
      // Todo consistent breakpoints
      padding: (props) => props.theme.spacingUnit * 2,
    },
  },
}));

function EshopContainer(props) {
  const [loading, setLoading] = React.useState(true);

  const { user } = useSession();
  const userLanguage = user && user.language;

  const [config, setConfig] = React.useState({
    isPaymentCardActivated: false,
  });

  const { match } = props;
  const { path } = match;

  const theme = useTheme();
  const classes = useStyles({ theme });
  const { i18n } = useTranslation();

  const addressPath = path + '/address';
  const paymentCardPath = path + '/payment-card';
  const eshopPath = path;

  React.useEffect(() => {
    EshopAPI.fetchPaymentCardConfiguration()
      .then((paymentCardConfiguration) => {
        setConfig({ ...paymentCardConfiguration });
      })
      .finally(() => {
        setLoading(false);
      });
  }, []);

  React.useEffect(() => {
    if (userLanguage) {
      setLoading(true);
      i18n.changeLanguage(userLanguage).finally(() => {
        setLoading(false);
      });
    }
  }, [userLanguage, i18n]);

  if (loading) {
    return <Loader />;
  }

  return (
    <>
      <TopBar isPaymentCardActivated={config.isPaymentCardActivated} />
      <div className={classes.container}>
        <Switch>
          <Route path={addressPath} component={Address} />
          {config.isPaymentCardActivated && (
            <Route path={paymentCardPath} component={PaymentCard} />
          )}
          <Route path={eshopPath} component={Eshop} />
          <Redirect to={{ pathname: path }} />
        </Switch>
      </div>
    </>
  );
}

export default EshopContainer;
