import React from 'react';

function usePagination(
  state = {
    page: 0,
    limit: 25,
  }
) {
  const [store, setStore] = React.useState(state);

  const changePage = React.useCallback(
    (page) => setStore((store) => ({ ...store, page })),
    []
  );

  const nextPage = React.useCallback(
    () => setStore((store) => ({ ...store, page: store.page + 1 })),
    []
  );

  const previousPage = React.useCallback(
    () => setStore((store) => ({ ...store, page: store.page - 1 })),
    []
  );

  const paginationHandler = React.useMemo(
    () => ({ changePage, nextPage, previousPage }),
    [nextPage, previousPage, changePage]
  );

  return [store, paginationHandler];
}

export default usePagination;
