import React from 'react';
import clsx from 'clsx';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import { makeStyles } from '@material-ui/core/styles';

import StarBorderIcon from '@material-ui/icons/StarBorder';
import StarIcon from '@material-ui/icons/Star';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';

import Paper from './Paper';

const backgrounds = [
  'linear-gradient(to right top, #e186d8, #e575c7, #e763b4, #e9509f, #e83a89, #e72b78, #e51a67, #e10055, #dd0045, #d80035, #d20024, #ca0810)',
  'linear-gradient(to right top, #3dda76, #00d3a0, #00c9c0, #00bcd2, #00aed5, #00a4d4, #089ad2, #228fce, #0f87cf, #027fcf, #0877cf, #1a6ecd)',
  'linear-gradient(to right top, #f2a826, #f09a1d, #ed8b15, #ea7c10, #e76d0e, #e96218, #ea5621, #ea4a29, #ef4038, #f33546, #f52a55, #f62063)',
  'linear-gradient(to right top, #cd26f2, #b320df, #9a19cc, #8211b9, #6b09a6, #5e069e, #510496, #43028e, #3b048f, #310690, #250991, #140b92)',
];

const cardBrandBackground = {
  visa: backgrounds[0],
  mastercard: backgrounds[1],
  'american express': backgrounds[2],
  default: backgrounds[3],
};

const getCardBackground = (brand) => {
  return (
    cardBrandBackground[brand.toLowerCase()] || cardBrandBackground['default']
  );
};

const useStyles = makeStyles((theme) => ({
  paper: {
    height: 180,
    display: 'flex',
    cursor: 'pointer',
    flexDirection: 'column',
    justifyContent: 'space-between',
    padding: theme.spacing(2),
    background: (props) => getCardBackground(props.brand),
    borderRadius: `${theme.spacing(3)}px !important`,
  },
  text: {
    color: 'white',
    fontWeight: 'bold',
  },
  number: {
    letterSpacing: theme.spacing(0.5),
  },
  icon: {
    color: '#ffc107',
  },
}));

function PaymentCard(props) {
  const { card, isDefault, onEdit, onDelete, onDefault } = props;
  const { brand = '', expiryDisplay = '', last4 = '' } = card;

  const classes = useStyles({ brand });

  const handleDelete = React.useCallback(
    (e) => {
      onDelete(e, card);
    },
    [onDelete, card]
  );

  const handleEdit = React.useCallback(
    (e) => {
      onEdit(e, card);
    },
    [onEdit, card]
  );

  const handleDefault = React.useCallback(
    (e) => {
      onDefault(e, card);
    },
    [onDefault, card]
  );

  return (
    <Paper className={classes.paper}>
      <div>
        <Grid container justify="space-between">
          <Grid item>
            <Typography variant="h6" className={classes.text} align="right">
              {card.brand}
            </Typography>
          </Grid>
          <Grid item>
            <IconButton
              size="small"
              className={classes.text}
              onClick={handleDefault}
            >
              {isDefault ? (
                <StarIcon className={classes.icon} />
              ) : (
                <StarBorderIcon className={classes.icon} />
              )}
            </IconButton>
            <IconButton
              size="small"
              className={classes.text}
              onClick={handleEdit}
            >
              <EditIcon />
            </IconButton>
            <IconButton
              size="small"
              className={classes.text}
              onClick={handleDelete}
            >
              <DeleteIcon />
            </IconButton>
          </Grid>
        </Grid>
      </div>
      <div>
        <Typography
          className={clsx(classes.text, classes.number)}
          align="center"
        >
          <b>{`      ${last4 || ''}`}</b>
        </Typography>
      </div>
      <div>
        <Grid container justify="space-between">
          <Grid item>
            <Typography className={classes.text}>{card.name}</Typography>
          </Grid>
          <Grid item>
            <Typography className={classes.text}>{expiryDisplay}</Typography>
          </Grid>
        </Grid>
      </div>
    </Paper>
  );
}

PaymentCard.defaultProps = {
  card: {},
  onEdit: () => {},
  onDelete: () => {},
};

export default PaymentCard;
