const useCheckoutPriceSummaryStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(2),
  },
  actionContainer: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  spaceBetween: {
    justifyContent: 'space-between',
  },
  termWrapper: {
    display: 'flex',
    alignItems: 'flex-start',
    flexDirection: 'column',
    marginTop: theme.spacing(2),
  },
  term: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-start',
  },
  termLink: {
    textDecoration: 'none',
    color: theme.palette.secondary.main,
  },
  actionWrapper: {
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
    marginTop: theme.spacing(2),
  },
  actionTitle: {
    marginBottom: theme.spacing(),
  },
  action: {
    marginBottom: theme.spacing(),
  },
}));

function CheckoutSummary(props) {
  const {
    total,
    totalTax,
    totalDiscount,
    totalWithoutTax,
    message,
    PaypalButtonProps,
    PayboxButtonProps,
    RequestQuotationButtonProps,
    PaymentCardButtonProps,
    ConfirmOrderButtonProps,
    Terms = [],
  } = props;

  const { t } = useTranslation();
  const classes = useCheckoutPriceSummaryStyles();

  const showTerms = Terms.some((term) => term.show);

  return (
    <Paper className={classes.paper}>
      <Typography variant="h5">{t('Order summary')}</Typography>
      <PriceSummary
        total={total}
        totalTax={totalTax}
        totalDiscount={totalDiscount}
        totalWithoutTax={totalWithoutTax}
        totalLabel={t('Total')}
        totalTaxLabel={t('Total tax')}
        totalWithoutTaxLabel={t('Total without tax')}
        totalDiscountLabel={t('Total discount')}
      />
      <div
        className={classnames(classes.actionContainer, {
          [classes.spaceBetween]: showTerms,
        })}
      >
        {showTerms && (
          <div className={classes.termWrapper}>
            {Terms.filter((term) => term.required && term.show).map(
              (term, i) => {
                return (
                  <div className={classes.term} key={i}>
                    <Checkbox
                      checked={term.checked}
                      onChange={(e) => term.setChecked(e.target.checked)}
                    />
                    <a
                      color="secondary"
                      href={term.fileURL}
                      target="_blank"
                      rel="noopener noreferrer"
                      className={classes.termLink}
                    >
                      {t(term.label)}
                    </a>
                  </div>
                );
              }
            )}
          </div>
        )}
        <div className={classes.actionWrapper}>
          {RequestQuotationButtonProps.showButton && (
            <div className={classes.action}>
              <RequestQuotationButton {...RequestQuotationButtonProps} />
            </div>
          )}
          {ConfirmOrderButtonProps.showButton && (
            <div className={classes.action}>
              <ConfirmOrderButton {...ConfirmOrderButtonProps} />
            </div>
          )}
          {PaymentCardButtonProps.showButton && (
            <div className={classes.action}>
              <PaymentCardButton {...PaymentCardButtonProps} />
            </div>
          )}
          {PayboxButtonProps.showButton && (
            <div className={classes.action}>
              <PayboxButton {...PayboxButtonProps} />
            </div>
          )}
          {PaypalButtonProps.showButton && (
            <div className={classes.action}>
              <PayPalButton
                style={{
                  color: 'blue',
                  shape: 'pill',
                  height: 36,
                }}
                {...PaypalButtonProps}
              />
            </div>
          )}
          {message && (
            <div className={classes.action}>
              <Typography>{message}</Typography>
            </div>
          )}
        </div>
      </div>
    </Paper>
  );
}

CheckoutSummary.defaultProps = {
  message: '',
  PaypalButtonProps: {},
  PayboxButtonProps: {},
  RequestQuotationButtonProps: {},
  PaymentCardButtonProps: {},
  ConfirmOrderButtonProps: {},
};

