import React, {
  useCallback,
  useState,
  useMemo,
  useEffect,
  useContext,
} from 'react';

import { useSession } from './SessionProvider';

export const LOCAL_STORAGE_CART = 'cart';
export const LOCAL_STORAGE_ADDRESS = 'address';
export const LOCAL_STORAGE_INVOICING_ADDRESS = 'invoicing';
export const LOCAL_STORAGE_DELIVERY_ADDRESS = 'delivery';

function getLocalStorageItem(key, defaultValue = {}) {
  try {
    return JSON.parse(localStorage.getItem(key)) || defaultValue;
  } catch (err) {
    return defaultValue;
  }
}

function setLocalStorageItem(key, value) {
  localStorage.setItem(key, JSON.stringify(value));
}

function getLocalStorageCart(userId) {
  return getLocalStorageItem(`${LOCAL_STORAGE_CART}_${userId}`);
}

function setLocalStorageCart(cart, userId) {
  setLocalStorageItem(`${LOCAL_STORAGE_CART}_${userId}`, cart);
}

function getLocalStorageAddress(userId) {
  return getLocalStorageItem(`${LOCAL_STORAGE_ADDRESS}_${userId}`);
}

function setLocalStorageAddress(address, userId) {
  setLocalStorageItem(`${LOCAL_STORAGE_ADDRESS}_${userId}`, address);
}

const EshopContext = React.createContext();

function EshopProvider(props) {
  const { children } = props;
  const {
    user: { id: userId },
  } = useSession();

  const [{ cart, config, checkoutAddresses }, setStore] = useState({
    cart: {},
    config: {},
    checkoutAddresses: {},
  });

  const setCart = useCallback((cart) => {
    setStore((store) => ({ ...store, cart }));
  }, []);

  const setCheckoutAddresses = useCallback((checkoutAddresses) => {
    setStore((store) => ({ ...store, checkoutAddresses }));
  }, []);

  const setConfig = useCallback(
    (config) => setStore((store) => ({ ...store, config })),
    []
  );

  const clearCart = useCallback(() => {
    setCart({});
  }, [setCart]);

  const setItem = useCallback((productId, qty) => {
    setStore((store) => {
      return { ...store, cart: { ...store.cart, [productId]: qty } };
    });
  }, []);

  const setInvoicingAddress = useCallback((addressId) => {
    setStore((store) => {
      return {
        ...store,
        checkoutAddresses: {
          ...store.checkoutAddresses,
          [LOCAL_STORAGE_INVOICING_ADDRESS]: addressId,
        },
      };
    });
  }, []);

  const setDeliveryAddress = useCallback((addressId) => {
    setStore((store) => {
      return {
        ...store,
        checkoutAddresses: {
          ...store.checkoutAddresses,
          [LOCAL_STORAGE_DELIVERY_ADDRESS]: addressId,
        },
      };
    });
  }, []);

  const clearItem = useCallback((productId) => {
    setStore((store) => {
      const cart = { ...store.cart };
      delete cart[productId];
      return { ...store, cart };
    });
  }, []);

  useEffect(() => {
    if (!userId) return;

    const cart = getLocalStorageCart(userId);
    const checkoutAddresses = getLocalStorageAddress(userId);

    if (cart && Object.keys(cart).length > 0) {
      setCart(cart); // Validate for quantity if required
    } else {
      setCart({});
    }

    if (checkoutAddresses && Object.keys(checkoutAddresses).length > 0) {
      setCheckoutAddresses(checkoutAddresses);
    } else {
      setCheckoutAddresses({});
    }
  }, [userId, setCart, setCheckoutAddresses]);

  useEffect(() => {
    if (userId && cart) {
      setLocalStorageCart(cart, userId);
    }
  }, [userId, cart]);

  useEffect(() => {
    if (userId && checkoutAddresses && Object.keys(checkoutAddresses).length) {
      setLocalStorageAddress(checkoutAddresses, userId);
    }
  }, [userId, checkoutAddresses]);

  const state = useMemo(
    () => ({
      cart,
      checkoutAddresses,
      config,
      setCart,
      clearCart,
      setConfig,
      setItem,
      clearItem,
      setInvoicingAddress,
      setDeliveryAddress,
    }),
    [
      cart,
      checkoutAddresses,
      config,
      setCart,
      clearCart,
      setConfig,
      setItem,
      clearItem,
      setInvoicingAddress,
      setDeliveryAddress,
    ]
  );

  return (
    <EshopContext.Provider value={state}>{children}</EshopContext.Provider>
  );
}

export function useEshop() {
  return useContext(EshopContext);
}

export default EshopProvider;
