import React, { useState, useCallback, useEffect, useMemo } from 'react';
import { useSnackbar } from 'notistack';

import {
  useEshop,
  LOCAL_STORAGE_DELIVERY_ADDRESS,
  LOCAL_STORAGE_INVOICING_ADDRESS,
} from '../providers/EshopProvider';
import Loader from '../components/Loader';
import Checkout from '../components/Checkout';
import EshopAPI, { PRODUCT_TYPESELECT_SERVICE } from '../EshopAPI';
import { useTranslation } from '../providers/TranslationProvider';
import { MESSAGE_TYPE as CheckoutMessageType } from './CheckoutMessage';

function useTerm() {
  const [checked, setChecked] = useState(false);
  const [file, setFile] = useState(null);
  const [required, setRequired] = useState(false);

  return [
    { checked, file, required },
    useMemo(() => ({ setChecked, setFile, setRequired }), []),
  ];
}

function CheckoutContainer(props) {
  const {
    match,
    history,
    onRedirectToOrder,
    onRedirectToQuotation,
    onRedirectToCheckoutMessage,
  } = props;
  const { url } = match;

  const { t } = useTranslation();
  const { enqueueSnackbar } = useSnackbar();
  const {
    cart,
    setItem,
    clearItem,
    clearCart,
    setCart,
    checkoutAddresses,
    setInvoicingAddress: setStorageInvoicingAddress,
    setDeliveryAddress: setStorageDeliveryAddress,
  } = useEshop();

  const [loading, setLoading] = useState(true);
  const [checkingOut, setCheckingOut] = useState(false);
  const [checkout, setCheckout] = useState({});
  const [deliveryAddresses, setDeliveryAddresses] = useState([]);
  const [invoicingAddresses, setInvoicingAddresses] = useState([]);
  const [deliveryAddress, setDeliveryAddress] = useState({});
  const [invoicingAddress, setInvoicingAddress] = useState({});
  const [paypalMerchantId, setPaypalMerchantId] = useState();
  const [canRequestQuotation, setCanRequestQuotation] = useState(false);
  const [confirmOrder, setConfirmOrder] = useState(false);

  const [dataTermState, dataTerm] = useTerm();
  const [returnPolicyTermState, returnPolicyTerm] = useTerm();
  const [termState, term] = useTerm();

  const getAddressById = useCallback((addressId, addresses = []) => {
    return addresses.find(
      (address) => address.id.toString() === addressId.toString()
    );
  }, []);

  const fetchCheckoutData = useCallback(() => {
    EshopAPI.checkCart(cart).then((checkout) => {
      const { itemsChanged, lines = [] } = checkout;
      const hasZeroQuantity = lines.find(
        (l) =>
          l.productTypeSelect !== PRODUCT_TYPESELECT_SERVICE &&
          Number(l.qty) === 0
      );
      if (itemsChanged || hasZeroQuantity) {
        const updatedCart = checkout.lines.reduce((_cart, line) => {
          if (Number(line.qty)) {
            _cart[line.productId] = Number(line.qty);
          }
          return _cart;
        }, {});
        setCart(updatedCart);
        /** Update checkout lines to override input value in checkout component */
        checkout.lines = checkout.lines.map((line) => {
          const { productId, qty } = line;
          const cartQty = cart[productId];

          if (Number(qty) < Number(cartQty)) {
            line.overrideInput = true;
          }
          return line;
        });
        enqueueSnackbar(t('One or multiple items have changed in your cart'), {
          variant: 'error',
        });
      }
      setCheckout(checkout);
    });
    setLoading(false);
  }, [cart, setCart, t, enqueueSnackbar]);

  const handleAddressChange = useCallback(
    (addressId, addresses = [], cb = () => {}) => {
      const address = addresses.find((address) => address.id === addressId);
      if (!address) {
        return;
      } else {
        cb(address);
      }
    },
    []
  );

  const handleDeliveryAddressChange = useCallback(
    (e) => {
      const { value: addressId } = e.target;

      const setAddress = (address) => {
        setDeliveryAddress(address);
        setStorageDeliveryAddress(addressId);
      };

      handleAddressChange(addressId, deliveryAddresses, setAddress);
    },
    [deliveryAddresses, handleAddressChange, setStorageDeliveryAddress]
  );

  const handleInvoicingAddressChange = useCallback(
    (e) => {
      const { value: addressId } = e.target;

      const setAddress = (address) => {
        setInvoicingAddress(address);
        setStorageInvoicingAddress(addressId);
      };

      handleAddressChange(addressId, invoicingAddresses, setAddress);
    },
    [invoicingAddresses, handleAddressChange, setStorageInvoicingAddress]
  );

  const handleEshopRedirection = useCallback(() => {
    history.replace(`/eshop`);
  }, [history]);

  const handleNewProduct = useCallback(() => {
    handleEshopRedirection();
  }, [handleEshopRedirection]);

  const handleNewAddress = useCallback(() => {
    history.push({
      pathname: `/eshop/address/new`,
      state: {
        fromUrl: url,
      },
    });
  }, [history, url]);

  const handleProductQuantityChange = useCallback(
    (productId, qty) => {
      setItem(productId, qty);
    },
    [setItem]
  );

  const handleProductDelete = useCallback(
    (productId) => {
      clearItem(productId);
    },
    [clearItem]
  );

  const handleCheckoutAction = useCallback(
    (order, isQuotation) => {
      clearCart();
      enqueueSnackbar(t('Thank you for shopping'), { variant: 'success' });
      if (isQuotation && onRedirectToOrder && order && order.id) {
        onRedirectToQuotation(order);
      } else if (onRedirectToOrder && order && order.id) {
        onRedirectToOrder(order);
      } else {
        handleEshopRedirection();
      }
    },
    [
      clearCart,
      onRedirectToOrder,
      onRedirectToQuotation,
      handleEshopRedirection,
      t,
      enqueueSnackbar,
    ]
  );

  const handlePaypalCheckout = useCallback(
    (paypalPaymentData) => {
      setCheckingOut(true);
      EshopAPI.checkoutPaypal({
        paypalPaymentData,
        orderData: {
          deliveryAddress,
          invoicingAddress,
          cart,
        },
      })
        .then((order) => {
          setCheckingOut(false);
          handleCheckoutAction(order);
        })
        .catch(() => {
          setCheckingOut(false);
          enqueueSnackbar(t('Error'), { variant: 'error' });
        });
    },
    [
      deliveryAddress,
      invoicingAddress,
      cart,
      handleCheckoutAction,
      t,
      enqueueSnackbar,
    ]
  );

  const handlePaymentCardCheckout = useCallback(() => {
    setCheckingOut(true);
    EshopAPI.checkoutPaymentCard({
      cardId: '',
      orderData: {
        deliveryAddress,
        invoicingAddress,
        cart,
      },
    })
      .then((order) => {
        setCheckingOut(false);
        onRedirectToCheckoutMessage(CheckoutMessageType.SUCCESS);
      })
      .catch(({ message = t('Your payment could not be completed') }) => {
        setCheckingOut(false);
        enqueueSnackbar(message, { variant: 'error' });
      });
  }, [
    t,
    deliveryAddress,
    invoicingAddress,
    cart,
    enqueueSnackbar,
    onRedirectToCheckoutMessage,
  ]);

  const handlePayboxCheckout = useCallback(() => {
    setCheckingOut(true);
    EshopAPI.checkoutPaybox({
      orderData: {
        deliveryAddress,
        invoicingAddress,
        cart,
      },
    })
      .then((result) => {
        if (result && result.url) {
          const link = document.createElement('a');
          link.href = result.url;
          link.click();
        } else {
          throw new Error('Checkout Error.');
        }
      })
      .catch(({ message = t('Your payment could not be completed') }) => {
        setCheckingOut(false);
        enqueueSnackbar(message, { variant: 'error' });
      });
  }, [t, deliveryAddress, invoicingAddress, cart, enqueueSnackbar]);

  const handleCheckoutQuotation = useCallback(() => {
    setCheckingOut(true);
    EshopAPI.checkoutQuotation({
      orderData: {
        deliveryAddress,
        invoicingAddress,
        cart,
      },
    })
      .then((quotation) => {
        setCheckingOut(false);
        handleCheckoutAction(quotation, true);
      })
      .catch(
        ({ message = t('Error requesting quotation. Please try again') }) => {
          setCheckingOut(false);
          enqueueSnackbar(message, { variant: 'error' });
        }
      );
  }, [
    t,
    deliveryAddress,
    invoicingAddress,
    cart,
    enqueueSnackbar,
    handleCheckoutAction,
  ]);

  const handleConfirmOrder = useCallback(() => {
    setConfirmOrder(true);
  }, []);

  useEffect(() => {
    EshopAPI.fetchAddresses().then((result) => {
      if (!(result.data && result.data.length)) {
        return;
      }

      const addresses = result.data.map((address) => {
        return Object.assign(address, {
          value: address.id,
          label: `${address.address || ''} ${address.addressComplement || ''}`,
        });
      });
      setDeliveryAddresses(addresses);
      setInvoicingAddresses(addresses);
    });
  }, []);

  useEffect(() => {
    const manageAddress = (addresses, addressId, setAddress) => {
      if (addresses.length) {
        if (addressId) {
          const address = getAddressById(addressId, addresses);
          setAddress(address || addresses[0]);
        } else {
          setAddress(addresses[0]);
        }
      }
    };
    manageAddress(
      deliveryAddresses,
      checkoutAddresses[LOCAL_STORAGE_DELIVERY_ADDRESS],
      setDeliveryAddress
    );

    manageAddress(
      invoicingAddresses,
      checkoutAddresses[LOCAL_STORAGE_INVOICING_ADDRESS],
      setInvoicingAddress
    );
  }, [
    deliveryAddresses,
    invoicingAddresses,
    checkoutAddresses,
    setStorageDeliveryAddress,
    setStorageInvoicingAddress,
    getAddressById,
  ]);

  useEffect(() => {
    EshopAPI.fetchPaypalMerchantId().then((result) => {
      setPaypalMerchantId(result.merchantId);
    });
  }, []);

  useEffect(() => {
    EshopAPI.fetchConfiguration().then((result) => {
      if (result.isQuotationRequestActive) {
        setCanRequestQuotation(true);
      }

      const {
        isDataPolicyRequired,
        dataPolicyMetaFile,
        isReturnPolicyRequired,
        returnPolicyMetaFile,
        isTermRequired,
        termMetaFile,
      } = result;

      if (isDataPolicyRequired) {
        dataTerm.setRequired(true);
        dataTerm.setFile(dataPolicyMetaFile);
      }

      if (isReturnPolicyRequired) {
        returnPolicyTerm.setRequired(true);
        returnPolicyTerm.setFile(returnPolicyMetaFile);
      }

      if (isTermRequired) {
        term.setRequired(true);
        term.setFile(termMetaFile);
      }
    });
  }, [dataTerm, returnPolicyTerm, term]);

  useEffect(() => {
    fetchCheckoutData();
  }, [fetchCheckoutData]);

  const disableCheckoutAction = !(
    checkout &&
    checkout.lines &&
    checkout.lines.length
  );

  const { paymentModes = [] } = checkout;

  const hidePaypalButton = !paymentModes.includes('paypal');
  const hidePayboxButton = !paymentModes.includes('paybox');
  const hideCreateOrderButton = !paymentModes.includes('stripe');

  const {
    original: { total: originalTotal = '0.00' } = {},
    currency: { code: currencyCode = 'EUR' } = {},
  } = checkout;
  const paypalClientId = process.env.REACT_APP_PAYPAL_CLIENT_ID;

  const PaypalButtonProps = useMemo(
    () => ({
      showButton:
        paypalClientId &&
        paypalMerchantId &&
        !disableCheckoutAction &&
        !hidePaypalButton &&
        confirmOrder,
      amount: originalTotal,
      onSuccess: handlePaypalCheckout,
      options: {
        clientId: paypalClientId,
        merchantId: paypalMerchantId,
        currency: currencyCode,
        disableFunding: 'card',
      },
    }),
    [
      handlePaypalCheckout,
      originalTotal,
      paypalClientId,
      paypalMerchantId,
      disableCheckoutAction,
      currencyCode,
      hidePaypalButton,
      confirmOrder,
    ]
  );

  const checkoutEnabled = Boolean(paymentModes && paymentModes.length);
  const summaryMessage = checkoutEnabled
    ? ''
    : t('Eshop not processing checkout');

  const PaymentCardButtonProps = useMemo(
    () => ({
      showButton: !hideCreateOrderButton && confirmOrder,
      disabled: disableCheckoutAction,
      onClick: handlePaymentCardCheckout,
    }),
    [
      disableCheckoutAction,
      handlePaymentCardCheckout,
      hideCreateOrderButton,
      confirmOrder,
    ]
  );

  const PayboxButtonProps = useMemo(
    () => ({
      showButton: !hidePayboxButton && confirmOrder,
      disabled: disableCheckoutAction,
      onClick: handlePayboxCheckout,
    }),
    [
      disableCheckoutAction,
      handlePayboxCheckout,
      hidePayboxButton,
      confirmOrder,
    ]
  );

  const termsAccepted = useMemo(
    () =>
      [dataTermState, returnPolicyTermState, termState].reduce(
        (accepted, state) => {
          if (!accepted) return false;
          if (state.required && !state.checked) {
            accepted = false;
          }
          return accepted;
        },
        true
      ),
    [dataTermState, returnPolicyTermState, termState]
  );

  const ConfirmOrderButtonProps = useMemo(
    () => ({
      showButton: !confirmOrder,
      disabled: !termsAccepted || disableCheckoutAction || !checkoutEnabled,
      onClick: handleConfirmOrder,
    }),
    [
      termsAccepted,
      confirmOrder,
      handleConfirmOrder,
      disableCheckoutAction,
      checkoutEnabled,
    ]
  );

  const RequestQuotationButtonProps = useMemo(
    () => ({
      showButton: canRequestQuotation && !confirmOrder,
      disabled: !termsAccepted || disableCheckoutAction,
      onClick: handleCheckoutQuotation,
    }),
    [
      termsAccepted,
      canRequestQuotation,
      disableCheckoutAction,
      handleCheckoutQuotation,
      confirmOrder,
    ]
  );

  const DataTerm = useMemo(
    () => ({
      ...dataTermState,
      ...dataTerm,
      show: !confirmOrder,
      fileURL: EshopAPI.getMetafileURL(dataTermState.file),
      label: t('Data Term'),
    }),
    [dataTermState, dataTerm, confirmOrder, t]
  );

  const ReturnPolicyTerm = useMemo(
    () => ({
      ...returnPolicyTermState,
      ...returnPolicyTerm,
      show: !confirmOrder,
      fileURL: EshopAPI.getMetafileURL(returnPolicyTermState.file),
      label: t('Return Policy Term'),
    }),
    [returnPolicyTermState, returnPolicyTerm, confirmOrder, t]
  );

  const Term = useMemo(
    () => ({
      ...termState,
      ...term,
      show: !confirmOrder,
      fileURL: EshopAPI.getMetafileURL(termState.file),
      label: t('Term'),
    }),
    [termState, term, confirmOrder, t]
  );

  const Terms = useMemo(() => [DataTerm, ReturnPolicyTerm, Term], [
    DataTerm,
    ReturnPolicyTerm,
    Term,
  ]);

  if (loading) {
    return <Loader />;
  }

  return (
    <Checkout
      total={checkout.total}
      totalTax={checkout.totalTax}
      totalDiscount={checkout.totalDiscount}
      totalWithoutTax={checkout.totalWithoutTax}
      productLines={checkout.lines}
      deliveryAddress={deliveryAddress}
      deliveryAddresses={deliveryAddresses}
      invoicingAddress={invoicingAddress}
      invoicingAddresses={invoicingAddresses}
      checkingOut={checkingOut}
      summaryMessage={summaryMessage}
      PaypalButtonProps={PaypalButtonProps}
      PayboxButtonProps={PayboxButtonProps}
      PaymentCardButtonProps={PaymentCardButtonProps}
      ConfirmOrderButtonProps={ConfirmOrderButtonProps}
      RequestQuotationButtonProps={RequestQuotationButtonProps}
      Terms={Terms}
      onNewAddress={handleNewAddress}
      onNewProduct={handleNewProduct}
      onProductDelete={handleProductDelete}
      onProductQuantityChange={handleProductQuantityChange}
      onDeliveryAddressChange={handleDeliveryAddressChange}
      onInvoicingAddressChange={handleInvoicingAddressChange}
    />
  );
}

export default CheckoutContainer;
