import React, { useState, useCallback } from 'react';
import { Switch, Route, withRouter } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Drawer from '@material-ui/core/Drawer';
import ListItemIcon from '@material-ui/core/ListItemIcon';

import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import ShoppingBasketIcon from '@material-ui/icons/ShoppingBasket';
import AddressIcon from '@material-ui/icons/Room';
import PaymentCardIcon from '@material-ui/icons/CreditCard';
import MenuIcon from '@material-ui/icons/Menu';

import Divider from '../components/Divider';
import BackButton from '../components/BackButton';
import { useEshop } from '../providers/EshopProvider';
import { useTranslation } from '../providers/TranslationProvider';

const useClientToolbarStyles = makeStyles({
  root: {
    width: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-start',
  },
});

function ClientToolbar(props) {
  const { onBack } = props;
  const classes = useClientToolbarStyles();
  return (
    <div className={classes.root}>
      <BackButton onClick={onBack} />
    </div>
  );
}

ClientToolbar.defaultProps = { onBack: () => {} };

const useShoppingCartStyles = makeStyles((theme) => ({
  badge: {
    color: '#FFFFFF',
    backgroundColor: theme.palette.secondary.main,
  },
}));

function ShoppingCart({ onClick, content }) {
  const classes = useShoppingCartStyles();

  const { cart } = useEshop();

  const cartLength = Object.values(cart || {}).reduce(
    (a, b) => Number(a) + Number(b),
    0
  );

  return (
    <IconButton size="small" onClick={onClick}>
      <Badge badgeContent={cartLength} classes={{ badge: classes.badge }}>
        <ShoppingCartIcon />
      </Badge>
    </IconButton>
  );
}

const useEshopToolbarStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    display: 'flex',
    alignItems: 'center',
  },
  grow: {
    flexGrow: 1,
  },
  eshopName: {
    color: theme.palette.secondary.main,
    fontWeight: 'bold',
  },
  sidebarPaper: {
    paddingTop: theme.spacing(5),
  },
  listItem: {
    '&:hover': {
      color: theme.palette.secondary.main,
      backgroundColor: theme.palette.secondary.light,
      '& .list-icon': {
        color: theme.palette.secondary.main,
      },
    },
  },
}));
function EshopToolbar(props) {
  const {
    showMenuButton,
    showBackButton,
    onBack,
    onCheckout,
    onLinkClick,
  } = props;

  const { t } = useTranslation();
  const classes = useEshopToolbarStyles();

  const [sideBarOpen, setSideBarOpen] = useState(false);

  const handleLinkClick = useCallback(
    (type) => {
      setSideBarOpen(false);
      setTimeout(() => {
        onLinkClick(type); // Redirect after sidebar closes
      }, 200);
    },
    [onLinkClick]
  );

  const links = [
    {
      label: t('Eshop'),
      Icon: ShoppingBasketIcon,
      onClick: () => handleLinkClick('eshop'),
    },
    {
      label: t('Addresses'),
      Icon: AddressIcon,
      onClick: () => handleLinkClick('address'),
    },
    {
      label: t('Payment Cards'),
      Icon: PaymentCardIcon,
      onClick: () => handleLinkClick('payment-card'),
    },
  ];

  return (
    <div className={classes.root}>
      {showMenuButton && (
        <IconButton
          onClick={() => {
            setSideBarOpen((open) => !open);
          }}
        >
          <MenuIcon />
        </IconButton>
      )}
      {showBackButton && <BackButton onClick={onBack} />}
      &nbsp;
      <div className={classes.grow}></div>
      <ShoppingCart onClick={onCheckout} />
      <Drawer
        anchor="left"
        open={sideBarOpen}
        onClose={() => setSideBarOpen(false)}
        ModalProps={{ disablePortal: true }}
        PaperProps={{
          className: classes.sidebarPaper,
        }}
      >
        <div>
          <Divider />
        </div>
        <List>
          {links.map(({ label, Icon, onClick }) => (
            <React.Fragment key={label}>
              <ListItem
                button
                key={label}
                className={classes.listItem}
                onClick={onClick}
              >
                <ListItemIcon className="list-icon">
                  <Icon />
                </ListItemIcon>
                <ListItemText primary={label} />
              </ListItem>
            </React.Fragment>
          ))}
        </List>
      </Drawer>
    </div>
  );
}

EshopToolbar.defaultProps = {
  showBackButton: true,
  showMenuButton: true,
  onBack: () => {},
  onCheckout: () => {},
  onLinkClick: () => {},
};

const useGeneralToolbarStyles = makeStyles((theme) => ({
  container: { display: 'flex', alignItems: 'center' },
  tabsIndicator: {
    backgroundColor: theme.palette.secondary.main,
  },
  tabRoot: {
    '&:hover': {
      backgroundColor: theme.palette.secondary.light,
      color: theme.palette.secondary.main,
    },
  },
  tabSelected: {
    backgroundColor: theme.palette.secondary.light,
    color: theme.palette.secondary.main,
  },
  rippleChild: {
    backgroundColor: theme.palette.secondary.main,
  },
}));

function GeneralToolbar(props) {
  const { activeLink, onClick, onCheckout, isPaymentCardActivated } = props;

  const { t } = useTranslation();
  const classes = useGeneralToolbarStyles();

  return (
    <div className={classes.container}>
      <Tabs
        value={activeLink}
        classes={{
          indicator: classes.tabsIndicator,
        }}
      >
        {[
          { route: 'eshop', label: t('Eshop') },
          { route: 'address', label: t('Addresses') },
          {
            route: 'payment-card',
            label: t('Payment Cards'),
            active: isPaymentCardActivated,
          },
        ].map(({ active = true, ...item }) => {
          return (
            active && (
              <Tab
                classes={{
                  root: classes.tabRoot,
                  selected: classes.tabSelected,
                }}
                key={item.route}
                label={item.label}
                value={item.route}
                onClick={() => {
                  onClick && onClick(item.route);
                }}
                TouchRippleProps={{
                  classes: {
                    child: classes.rippleChild,
                  },
                }}
              />
            )
          );
        })}
      </Tabs>
      &nbsp;
      <ShoppingCart onClick={onCheckout} />
    </div>
  );
}

Topbar.defaultProps = { activeLink: 'shop', onClick: () => {} };

const useStyles = makeStyles((theme) => ({
  appbar: {
    zIndex: 1000,
    backgroundColor: '#FFFFFF',
  },
  toolbar: {
    width: '100%',
    height: '100%',
    margin: 'auto',
    justifyContent: 'flex-end',
    maxWidth: 1400 + theme.spacing(2),
  },
}));

function Topbar(props) {
  const { location, history, match, isPaymentCardActivated } = props;
  const { url, path } = match;
  const { pathname } = location;

  const classes = useStyles();

  const getActiveLink = useCallback(() => {
    if (pathname.includes('eshop/address')) {
      return 'address';
    } else if (pathname.includes('eshop/payment-card')) {
      return 'payment-card';
    } else {
      return 'eshop';
    }
  }, [pathname]);

  const handleLinkClick = useCallback(
    (type = 'eshop') => {
      if (type === 'address') {
        history.push(`${path}/address`);
      } else if (type === 'payment-card') {
        history.push(`${path}/payment-card`);
      } else {
        history.push(path);
      }
    },
    [history, path]
  );

  const handleCheckout = useCallback(() => {
    history.push(`${url}/checkout`);
  }, [history, url]);

  const handleRedirectToAddressList = useCallback(() => {
    history.replace(`${url}/address`);
  }, [history, url]);

  const handleRedirectToPaymentCardList = useCallback(() => {
    history.replace(`${url}/payment-card`);
  }, [history, url]);

  const handleRedirectToEshop = useCallback(() => {
    history.replace({
      pathname: url,
      state: location.state,
      search: location.search,
    });
  }, [history, url, location]);

  const handleAddressFormOnBack = useCallback(() => {
    const fromUrl = location && location.state && location.state.fromUrl;
    if (fromUrl) {
      history.replace(fromUrl);
    } else {
      handleRedirectToAddressList();
    }
  }, [history, location, handleRedirectToAddressList]);

  const activeLink = getActiveLink();

  const generalToolbarProps = {
    activeLink,
    onClick: handleLinkClick,
    onCheckout: handleCheckout,
    isPaymentCardActivated,
  };

  return (
    <AppBar className={classes.appbar} position="relative" color="default">
      <Toolbar className={classes.toolbar} variant="dense">
        <Switch>
          <Route
            exact
            path={path}
            render={(props) => (
              <GeneralToolbar {...props} {...generalToolbarProps} />
            )}
          />
          <Route
            exact
            path={`${path}/address`}
            render={(props) => (
              <GeneralToolbar {...props} {...generalToolbarProps} />
            )}
          />
          <Route
            path={`${path}/address/:addressId`}
            render={(props) => (
              <ClientToolbar {...props} onBack={handleAddressFormOnBack} />
            )}
          />
          <Route
            exact
            path={`${path}/payment-card`}
            render={(props) => (
              <GeneralToolbar {...props} {...generalToolbarProps} />
            )}
          />
          <Route
            path={`${path}/payment-card/:paymentCardId`}
            render={(props) => (
              <ClientToolbar
                {...props}
                onBack={handleRedirectToPaymentCardList}
              />
            )}
          />
          <Route path={`${path}/checkout/:message`} render={(props) => null} />
          <Route
            path={`${path}/checkout`}
            render={(props) => (
              <EshopToolbar
                {...props}
                onBack={handleRedirectToEshop}
                onCheckout={handleCheckout}
                onLinkClick={handleLinkClick}
                showMenuButton={false}
              />
            )}
          />
          <Route
            path={`${path}/product/:productId`}
            render={(props) => (
              <EshopToolbar
                {...props}
                onBack={handleRedirectToEshop}
                onCheckout={handleCheckout}
                onLinkClick={handleLinkClick}
                showMenuButton={false}
              />
            )}
          />
        </Switch>
      </Toolbar>
    </AppBar>
  );
}

Topbar.defaultProps = {
  activeLink: 'shop',
  onClick: () => {},
  onCheckout: () => {},
};

export default withRouter(Topbar);
