import React from 'react';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import NumberFormat from 'react-number-format';

import Input from './Input';
import Paper from './Paper';
import { useTranslation } from '../providers/TranslationProvider';

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(3),
  },
  error: {
    color: theme.palette.error.main,
  },
}));

const MaskInput = (props) => {
  const { onChange, inputRef, ...other } = props;

  return (
    <NumberFormat
      {...other}
      getInputRef={inputRef}
      onValueChange={(values) => {
        onChange({
          target: {
            name: props.name,
            value: values.value,
          },
        });
      }}
      isNumericString
    />
  );
};

function limit(val, max) {
  if (val.length === 1 && val[0] > max[0]) {
    val = '0' + val;
  }

  if (val.length === 2) {
    if (Number(val) === 0) {
      val = '01';

      //this can happen when user paste number
    } else if (val > max) {
      val = max;
    }
  }

  return val;
}

function cardExpiry(val) {
  const month = limit(val.substring(0, 2), '12');
  const year = val.substring(2, 4);

  return month + (year.length ? '/' + year : '');
}

function PaymentCardForm(props) {
  const { errors, values, disabled, onChange, onSubmit } = props;

  const classes = useStyles();
  const { t } = useTranslation();

  return (
    <Paper className={classes.paper}>
      <form onSubmit={onSubmit} noValidate>
        <Grid container spacing={2} justify="center">
          <Grid item xs={12}>
            <Input
              name="name"
              label={t('Name')}
              disabled={disabled}
              value={values.name}
              error={errors.name}
              onChange={onChange}
              required
            />
          </Grid>
          <Grid item xs={12}>
            <Input
              name="cardNumber"
              label={t('Card number')}
              disabled={disabled}
              value={values.cardNumber}
              error={errors.cardNumber}
              onChange={onChange}
              required
              InputProps={{
                inputComponent: MaskInput,
                inputProps: {
                  format: '#### #### #### ####',
                  placeholder: '1234 1234 1234 1234',
                },
              }}
            />
          </Grid>

          <Grid item xs={6}>
            <Input
              name="expiry"
              label={t('Expiry')}
              disabled={disabled}
              value={values.expiry}
              error={errors.expiry}
              onChange={onChange}
              required
              InputProps={{
                inputComponent: MaskInput,
                inputProps: {
                  format: cardExpiry,
                  placeholder: 'MM/YY',
                },
              }}
            />
          </Grid>
          <Grid item xs={6}>
            <Input
              name="cvc"
              label={t('CVC')}
              disabled={disabled}
              value={values.cvc}
              error={errors.cvc}
              onChange={onChange}
              required
              InputProps={{
                inputComponent: MaskInput,
                inputProps: {
                  format: '####',
                  placeholder: 'CVC',
                },
              }}
            />
          </Grid>
          <Grid item xs={12} align="right">
            <Button raised type="submit" disabled={disabled}>
              {t('Save')}
            </Button>
          </Grid>
        </Grid>
      </form>
    </Paper>
  );
}

PaymentCardForm.defaultProps = {
  errors: {},
  values: {},
  disabled: false,
  onChange: () => {},
  onSubmit: () => {},
};

export default PaymentCardForm;
