import React, { useCallback, useEffect, useState } from 'react';
import { useFormik } from 'formik';
import Grid from '@material-ui/core/Grid';
import { useSnackbar } from 'notistack';

import Loader from '../components/Loader';
import PaymentCardForm from '../components/PaymentCardForm';
import EshopAPI from '../EshopAPI';
import { useTranslation } from '../providers/TranslationProvider';
import { inRange } from './utils';

function PaymentCardFormContainer(props) {
  const { redirectToCardList, match } = props;
  const { params } = match;
  const { cardId } = params;

  const { t } = useTranslation();
  const { enqueueSnackbar } = useSnackbar();

  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [card, setCard] = useState({
    name: '',
    cardNumber: '',
    expiry: '',
    cvc: '',
  });
  const [validateOnChange, setValidateOnChange] = useState(false);

  async function handleSubmitPaymentCard(values) {
    setSaving(true);
    const service = card.id
      ? EshopAPI.updatePaymentCard(card.id, values)
      : EshopAPI.addPaymentCard(values);

    service
      .then(() => {
        setSaving(false);
        redirectToCardList();
      })
      .catch((error) => {
        enqueueSnackbar(
          t('Error') + `${error.message ? ` :  ${error.message}` : ''}`,
          {
            variant: 'error',
          }
        );
        setSaving(false);
      });
  }

  const {
    values,
    errors,
    handleSubmit: handleSubmitFormik,
    handleChange,
  } = useFormik({
    validateOnChange,
    initialValues: {
      name: card.name,
      cardNumber: card.cardNumber,
      expiry: card.expiry,
      cvc: card.cvc,
    },
    validate: (values) => {
      const errors = {};

      if (!values.name) {
        errors.name = `${t('Name')} ${t('Required')}`;
      }

      if (!values.cardNumber) {
        errors.cardNumber = `${t('Card number')} ${t('Required')}`;
      } else if (!inRange(String(values.cardNumber).length, 14, 16)) {
        errors.cardNumber = `${t('Invalid card number')}`;
      }

      if (!values.expiry) {
        errors.expiry = `${t('Expiry')} ${t('Required')}`;
      } else if (String(values.expiry).length < 4) {
        errors.expiry = `${t('Invalid expiry')}`;
      }

      if (!values.cvc) {
        errors.cvc = `${t('CVC')} ${t('Required')}`;
      } else if (String(values.cvc).length < 3) {
        errors.cvc = `${t('Invalid cvc')}`;
      }

      return errors;
    },
    onSubmit: async (values) => {
      await handleSubmitPaymentCard(values);
    },
    enableReinitialize: true,
  });

  useEffect(() => {
    if (!cardId) {
      // new card
      setLoading(false);
    } else {
      EshopAPI.fetchPaymentCard(cardId)
        .then((card) => {
          setCard(card);
          setLoading(false);
        })
        .catch(redirectToCardList);
    }
  }, [cardId, redirectToCardList]);

  const handleSubmit = useCallback(
    (e) => {
      e.preventDefault();
      setValidateOnChange(true);
      handleSubmitFormik();
    },
    [handleSubmitFormik]
  );

  if (loading) {
    return <Loader />;
  }

  return (
    <Grid container>
      <Grid item xs={12} md={3}></Grid>
      <Grid item xs={12} md={6}>
        <PaymentCardForm
          values={values}
          errors={errors}
          disabled={saving}
          onChange={handleChange}
          onSubmit={handleSubmit}
        />
      </Grid>
      <Grid item xs={12} md={3}></Grid>
    </Grid>
  );
}

export default PaymentCardFormContainer;
