import React from 'react';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';

import Divider from './Divider';

const useStyles = makeStyles((theme) => ({
  row: {
    display: 'flex',
    marginBottom: theme.spacing(0.5),
    justifyContent: 'space-between',
  },
}));

function PriceSummary(props) {
  const {
    total,
    totalLabel,
    totalWithoutTax,
    totalDiscount,
    totalWithoutTaxLabel,
    totalTax,
    totalTaxLabel,
    totalDiscountLabel,
    divider,
    className,
  } = props;

  const classes = useStyles();

  return (
    <div className={className}>
      {divider && <Divider />}
      <div className={classes.row}>
        <Typography>{totalWithoutTaxLabel}</Typography>
        <Typography>{totalWithoutTax}</Typography>
      </div>
      <div className={classes.row}>
        <Typography>{totalDiscountLabel}</Typography>
        <Typography>{totalDiscount}</Typography>
      </div>
      <div className={classes.row}>
        <Typography>{totalTaxLabel}</Typography>
        <Typography>{totalTax}</Typography>
      </div>
      {divider && <Divider />}
      <div className={classes.row}>
        <Typography>
          <b>{totalLabel}</b>
        </Typography>
        <Typography>
          <b>{total}</b>
        </Typography>
      </div>
    </div>
  );
}

PriceSummary.defaultProps = {
  divider: true,
  total: '0.00',
  totalTax: '0.00',
  totalWithoutTax: '0.00',
  totalDiscount: '0.00',
  totalLabel: 'Total',
  totalTaxLabel: 'Total Tax',
  totalWithoutTaxLabel: 'Total Without Tax',
  totalDiscountLabel: 'Total Discount',
};

export default PriceSummary;
