import axios from 'axios';

let lastCookieString;
let lastCookies = {};

export function readCookie(name) {
  let cookieString = document.cookie || '';
  if (cookieString !== lastCookieString) {
    lastCookieString = cookieString;
    lastCookies = cookieString.split('; ').reduce((obj, value) => {
      let parts = value.split('=');
      obj[parts[0]] = parts[1];
      return obj;
    }, {});
  }
  return lastCookies[name];
}

export class AxelorRest {
  constructor(http, entity) {
    this.http = http;
    this.entity = entity;
  }

  insert(data) {
    return this.http.put('/ws/rest/' + this.entity, data);
  }

  update(data) {
    return this.http.post('/ws/rest/' + this.entity, data);
  }

  delete(id) {
    return this.http.delete('/ws/rest/' + this.entity + '/' + id);
  }

  deleteAll(records) {
    return this.http.post(`/ws/rest/${this.entity}/removeAll`, {
      records: records.map(({ id, version }) => ({ id, version })),
    });
  }

  fetch(id, options) {
    return this.http.post(
      '/ws/rest/' + this.entity + '/' + id + '/fetch',
      options
    );
  }

  copy(id, options) {
    return this.http.get(
      '/ws/rest/' + this.entity + '/' + id + '/copy',
      options
    );
  }

  details(id, fieldName) {
    return this.http.get(
      '/ws/rest/' + this.entity + '/' + id + '/details?name=' + fieldName
    );
  }

  search(options = {}) {
    return this.http.post(
      '/ws' + (this.entity ? `/rest/${this.entity}` : '') + '/search',
      options
    );
  }
}

export class Http {
  constructor({ baseURL = '/' } = {}, interceptor = null) {
    this.instance = axios.create({
      baseURL,
    });
    this.interceptor = interceptor;
  }

  catchError(e) {
    console.log('catch error', e);
    if (this.callback) {
      this.callback(e);
    } else if (`${e}`.includes('401')) {
      // window.location.reload();
    }
  }

  request(url, data = {}, config = {}, overrideHeaders) {
    const {
      headers: $headers = {},
      token = true,
      withCredentials = true,
    } = config;
    const headers = {
      ...(token ? { 'X-CSRF-Token': readCookie('CSRF-TOKEN') } : {}),
      ...(overrideHeaders ||
        Object.assign(
          {},
          {
            accept: 'application/json',
            'x-requested-with': 'XMLHttpRequest',
          },
          $headers,
          Object.keys(data).length ? { 'content-type': 'application/json' } : {}
        )),
    };

    const options = Object.assign(
      {
        url,
        method: 'POST',
        headers,
        data:
          headers['content-type'] === 'application/json'
            ? JSON.stringify(data)
            : data,
        // timeout: 60000,
      },
      config,
      { withCredentials }
    );

    if (
      (options.method === 'GET' || options.method === 'HEAD') &&
      options.data
    ) {
      delete options.data;
    }

    return this.instance(options);
  }

  intercept(url, method = 'GET', data = {}, headers, config = {}) {
    const _config = Object.assign(
      {
        method,
        withCredentials: true,
      },
      config
    );

    const doRequest = () => this.request(url, data, _config, headers);
    const timeoutRequest = (delay = 100000) =>
      new Promise((resolve, reject) => {
        setTimeout(() => {
          reject(new Error('Request failed Timeout'));
        }, delay);
      });

    return Promise.race([
      timeoutRequest(),
      doRequest().then((res) => {
        if (res.status === 401) {
          return this.catchError({ logout: true });
        }
        return res.data;
      }),
    ]).catch(this.catchError.bind(this));
  }

  get(url, headers, config) {
    return this.intercept(url, 'GET', {}, headers, config);
  }

  post(url, data, headers) {
    return this.intercept(url, 'POST', data, headers);
  }

  patch(url, data, headers) {
    return this.intercept(url, 'PATCH', data, headers);
  }

  put(url, data, headers) {
    return this.intercept(url, 'PUT', data, headers);
  }

  delete(url, headers) {
    return this.intercept(url, 'DELETE', {}, headers);
  }

  head(url, headers) {
    return this.intercept(url, 'HEAD', {}, headers);
  }

  action(url, data) {
    return this.intercept('/ws/action' + url, 'POST', data);
  }

  rest(entity) {
    return new AxelorRest(this, entity);
  }

  login(data = {}) {
    var config = Object.assign({}, this.config || {}, data);
    return this.request(
      '/callback',
      {
        username: config.username,
        password: config.password,
      },
      { token: false }
    )
      .then((res) => {
        var isAuth = null;
        if (res === undefined) {
          throw new Error('Failed To Connect !!');
        }
        if (res && res.status === 401) {
          isAuth = false;
        } else if (res && res.status === 200) {
          isAuth = true;
          this.config = config;
          /*
           Custom Cookie
           res.headers['csrf-token'] &&
             (document.cookie = `CSRF-TOKEN=${res.headers['csrf-token']}`);
          */
        }
        return isAuth;
      })
      .catch(this.catchError.bind(this));
  }

  logout() {
    return this.get('/logout');
  }

  info() {
    return this.get('/ws/app/info');
  }

  checkInfo() {
    return this.request('/ws/app/info', {}, { method: 'GET' })
      .then((res) => res.data)
      .then((res) => ({
        appUser: res,
        app: {
          name: res['application.name'],
          version: res['application.version'],
          description: res['application.description'],
        },
        user: {
          id: res['user.id'],
          code: res['user.login'],
          name: res['user.name'],
          language: res['user.lang'] || 'en',
          email: res['user.login'],
          theme: res['user.theme'] || '',
        },
      }));
  }

  upload(data = null, headers = {}, callback = () => true, info = {}) {
    return new Promise((resolve, reject) => {
      const xhr = new XMLHttpRequest(),
        method = 'POST',
        url = '/ws/files/upload';

      const doClean = () =>
        headers['X-File-Id']
          ? this.intercept(() => this.delete(`${url}/${headers['X-File-Id']}`))
          : Promise.resolve(true);

      const formatSize = (done, total) => {
        const format = (size) => {
          if (size > 1000000000)
            return parseFloat(size / 1000000000).toFixed(2) + ' GB';
          if (size > 1000000)
            return parseFloat(size / 1000000).toFixed(2) + ' MB';
          if (size >= 1000) return parseFloat(size / 1000).toFixed(2) + ' KB';
          return size + ' B';
        };
        return format(done || 0) + '/' + format(total);
      };

      xhr.open(method, url, true);

      Object.keys(headers).forEach((k) => {
        xhr.setRequestHeader(k, headers[k]);
      });

      xhr.withCredentials = true;
      xhr.overrideMimeType('application/octet-stream');
      xhr.setRequestHeader('Content-Type', 'application/octet-stream');
      xhr.setRequestHeader('X-Requested-With', 'XMLHttpRequest');
      xhr.setRequestHeader('X-CSRF-Token', readCookie('CSRF-TOKEN'));

      xhr.onload = () => {
        callback(100);
      };

      info.abort = () => {
        xhr.abort();
        return doClean();
      };

      xhr.onreadystatechange = function () {
        if (xhr.readyState === 4) {
          switch (xhr.status) {
            case 401:
              this.callbackInterceptor();
              throw new Error('Unauthorized !!');
            case 200:
              try {
                const result = JSON.parse(xhr.responseText);
                resolve(result);
              } catch (e) {
                resolve(xhr.responseText);
              }
              break;
            default:
              doClean();
              reject({ status: xhr.status });
              break;
          }
        }
      };

      xhr.upload.onprogress = (e) => {
        const fileSize = headers['X-File-Size'];
        const total = parseFloat(headers['X-File-Offset']) + e.loaded;
        const done = Math.round((total / fileSize) * 100);

        info.progress = done > 95 ? '95%' : done + '%';
        info.transfer = formatSize(total, fileSize);
        info.loaded = total === fileSize;

        if (e.lengthComputable) {
          callback((e.loaded / e.total) * 100, info);
        }
      };

      xhr.send(data);
    });
  }
}

export default new Http({
  baseURL: process.env.NODE_ENV === 'production' ? '../' : '/axelor-erp', // '/' for portal
});
