import React, { useCallback, useState, useEffect } from 'react';
import { Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

import Pagination from '../components/Pagination';
import CategoryList from '../components/CategoryList';
import ProductSearch from '../components/ProductSearch';
import Loader from '../components/Loader';
import ProductList from '../components/ProductList';
import EshopAPI from '../EshopAPI';
import usePagination from '../hooks/usePagination';
import { useEshop } from '../providers/EshopProvider';
import { useTranslation } from '../providers/TranslationProvider';

const useStyles = makeStyles({
  header: {
    display: 'flex',
    flexDirection: 'column',
  },
  filters: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    flexWrap: 'wrap',
  },
});

const ALL_CATEGORY = {
  id: -1,
  name: 'All',
};

function ProductListContainer(props) {
  const { match, history, location } = props;
  const { url } = match;

  const classes = useStyles();
  const { t } = useTranslation();
  const [paginationStore, paginationHandler] = usePagination({
    limit: 25,
    page: (location.state || {}).page || 0,
  });

  const { limit, page } = paginationStore;

  const { cart = {}, config = {} } = useEshop();

  const [
    { loadingProducts, products, totalProducts },
    setProductsStore,
  ] = useState({
    products: [],
    totalProducts: 0,
    loadingProducts: true,
  });

  const previouslySetCategoryId = Number(
    location &&
      location.search &&
      new URLSearchParams(location.search).get('categoryid')
  );

  const [
    { loadingCategories, categories, selectedCategoryId },
    setCategoriesStore,
  ] = useState({
    categories: [ALL_CATEGORY],
    selectedCategoryId: previouslySetCategoryId || -1,
    loadingCategories: true,
  });

  const [searchInput, setSearchInput] = useState(undefined);

  const handleChangePage = useCallback(
    (page) => {
      paginationHandler.changePage(page);
      history.replace({
        pathname: url,
        state: { page },
        search: location.search,
      });
    },
    [url, history, location, paginationHandler]
  );

  useEffect(() => {
    async function fetchCategories() {
      setCategoriesStore((store) => ({ ...store, loadingCategories: true }));
      EshopAPI.fetchCategories()
        .then((categories) => {
          setCategoriesStore((store) => ({
            ...store,
            categories: [ALL_CATEGORY].concat(categories),
            loadingCategories: false,
          }));
        })
        .catch(() => {
          setCategoriesStore((store) => ({
            ...store,
            loadingCategories: false,
          }));
        });
    }

    fetchCategories();
  }, []);

  useEffect(() => {
    async function fetchProducts() {
      setProductsStore((store) => ({ ...store, loadingProducts: true }));

      const query = {
        limit,
        page: page + 1,
        searchInput,
      };

      const handleResult = ({ data, total, offset }) => {
        if (total < offset) {
          handleChangePage(0);
        } else {
          setProductsStore({
            products: data.map((product) => ({
              ...product,
              count: cart[product.id],
            })),
            totalProducts: total,
            loadingProducts: false,
          });
        }
      };

      const promise =
        selectedCategoryId === -1
          ? EshopAPI.fetchProducts(query)
          : EshopAPI.fetchCategoryProducts(selectedCategoryId, query);

      promise.then(handleResult).catch(() => {
        setProductsStore((store) => ({ ...store, loadingProducts: false }));
      });
    }

    fetchProducts();
  }, [selectedCategoryId, limit, page, searchInput, cart, handleChangePage]);

  const handleCategoryClick = useCallback(
    (chipId = -1) => {
      history.replace({
        pathname: url,
        search: `?categoryid=${Number(chipId)}`,
      });
      paginationHandler.changePage(0);
      setCategoriesStore((store) => ({
        ...store,
        selectedCategoryId: Number(chipId),
      }));
    },
    [history, url, paginationHandler]
  );

  const handleProductClick = useCallback(
    (e, productId) => {
      history.push({
        pathname: `${url}/product/${productId}`,
        state: { page },
        search: location.search,
      });
    },
    [history, url, page, location]
  );

  return (
    <>
      <div className={classes.header}>
        <div className={classes.filters}>
          {loadingCategories ? (
            <Loader />
          ) : (
            <CategoryList
              categories={categories}
              selectedCategoryId={selectedCategoryId}
              onCategoryClick={handleCategoryClick}
            />
          )}

          <ProductSearch value={searchInput} onChange={setSearchInput} />
        </div>

        <Pagination
          label={`${t('Products')} : ${
            totalProducts ? page * limit + 1 : 0
          } to ${
            Math.min(page * limit + limit, totalProducts) || 0
          } of ${totalProducts}`}
          onNext={paginationHandler.nextPage}
          onPrevious={paginationHandler.previousPage}
          disablePrevious={page === 0}
          disableNext={(page + 1) * limit >= totalProducts}
        />
      </div>
      <div>
        <Typography gutterBottom>
          <i>{`${
            config.isAti
              ? t('All prices include taxes')
              : t('All prices are tax free')
          }`}</i>
        </Typography>
      </div>
      <ProductList
        loading={loadingProducts}
        onClick={handleProductClick}
        products={products}
      />
    </>
  );
}

export default ProductListContainer;
