import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';

import ProductCard from './ProductCard';

const useBadgeStyles = makeStyles((theme) => ({
  badge: {
    color: 'white',
    width: 'fit-content',
    minWidth: 18,
    height: 18,
    fontSize: '0.675rem',
    borderRadius: 20,
    display: 'inline-flex',
    alignItems: 'center',
    justifyContent: 'center',
    paddingLeft: 2,
    paddingRight: 2,
    boxSizing: 'border-box',
    backgroundColor: theme.palette.secondary.main,
  },
}));

function Badge(props) {
  const { value, className } = props;

  const classes = useBadgeStyles();

  return <div className={clsx(classes.badge, className)}>{value}</div>;
}

const useStyles = makeStyles((theme) => ({
  container: {
    position: 'relative',
    width: '100%',
  },
  badge: {
    position: 'absolute',
    top: theme.spacing(),
    right: theme.spacing(),
    zIndex: 1,
  },
}));

export default ({ count = 0, ...props }) => {
  const classes = useStyles();

  return (
    <div className={classes.container}>
      {count ? <Badge value={count} className={classes.badge} /> : null}
      <ProductCard {...props} />
    </div>
  );
};
